.o-switch {
  .labels {
    padding-right: px-to-rem(13);
    padding-left: px-to-rem(13);
  }

  .toggle {
    width: px-to-rem(70);

    &::before {
      width: px-to-rem(24);
      height: px-to-rem(24);
    }

    .off,
    .on {
      margin-top: px-to-rem(1);
      margin-bottom: px-to-rem(3);
    }
  }
}
