/**
 * Boosted Overload
 * Following code is needed to configure some background color that can't be configured just by changing variables
 */
// @mixin ob1-button-variant($background, $border, $hover-background, $hover-border, $active-background, $active-border, $disabled-background, $disabled-border, $disabled-color, $color, $hover-color, $active-color)
@mixin ob1-button-variant($background, $border, $hover-background, $hover-border, $active-background, $active-border, $disabled-background, $disabled-border, $disabled-color, $color: color-yiq($background)) {
  @include button-variant($background, $border, $hover-background, $hover-border, $active-background, $active-border, $disabled-background, $disabled-border, $disabled-color, $color);

  &:focus:not(:hover):not(:active):not(.active):not(:focus-visible) {
    color: $color;
    background-color: $background;
    border-color: $border;
  }
}

// @mixin ob1-outline-button-variant($background, $border, $hover-background, $hover-border, $active-background, $active-border, $disabled-background, $disabled-border, $disabled-color, $color, $hover-color, $active-color)
@mixin ob1-outline-button-variant($background, $border, $hover-background, $hover-border, $active-background, $active-border, $disabled-background, $disabled-border, $disabled-color, $color: color-yiq($background), $hover-color: $hover-border, $active-color: $active-border) {
  @include ob1-button-variant($background, $border, $hover-background, $hover-border, $active-background, $active-border, $disabled-background, $disabled-border, $disabled-color, $color);

  // overload Bootstrap default color selection by contrast
  &:not(:disabled):not(.disabled):hover,
  &:not(:disabled):not(.disabled):focus {
    color: $hover-color;
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active,
  .show > &.dropdown-toggle {
    color: $active-color;
  }
}
