/* The snackbar - position it at the bottom and in the middle of the screen */
.ob1-snackbar {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10200;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  padding: 15px;
  color: $white;
  visibility: hidden;
  background-color: $gray1;
  transition: .15s;
  transition-timing-function: ease-in;
  transform: translateY(130%);

  .ob1-snackbar-icon {
    display: block;
    padding-right: px-to-rem(15);
    font-size: 30px;
  }

  .ob1-snackbar-link {
    padding-top: 0;
    padding-bottom: 0;
    margin-left: auto;
    text-align: right;
  }

  .ob1-snackbar-wrapper {
    display: flex;
    align-items: center;
    padding-right: 15px;
  }

  .ob1-snackbar-label {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 3px;
    margin-bottom: 6px;
  }
}

/* Show the snackbar when clicking on a button (class added with JavaScript) */
.ob1-snackbar.show {
  bottom: 0;
  visibility: visible;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .4);
  opacity: 1;
  transition: .3s;
  transition-timing-function: ease-out;
  transform: translateY(0);
  &:hover {
    animation-play-state: paused;
  }
}

@include media-breakpoint-up(sm) {
  .ob1-snackbar.show {
    bottom: 30px;
  }

  .ob1-snackbar {
    flex-wrap: nowrap;
    max-width: calc((100% - 3.125%) * (5 / 6) - 30px); // 10/12ème du conteneur (sans les marges de sécurité, 2 * 1.5625%) - 30px de goutière
    margin: auto;
    .ob1-snackbar-label {
      margin-bottom: 7px;
    }
  }
}

@include media-breakpoint-up(xl) {
  .ob1-snackbar {
    max-width: calc((100% - 6.25%) * (2 / 3) - 30px); // 8/12ème du conteneur (sans les marges de sécurité, 2 * 3.125%) - 30px de goutière
  }
}
