.alert-container {
  display: flex;
  flex-direction: column;
  padding: px-to-rem(15);
  margin-bottom: px-to-rem(15);

  &.ob1-notification-message {
    padding: 0;

    .alert-icon {
      padding-right: px-to-rem(15);
      margin: 0;

      &::before {
        color: $dark;
      }
    }

    .ob1-alert-title,
    .alert-content {
      padding-right: px-to-rem(23);
    }

    a {
      padding: px-to-rem(15);

      .alert::after {
        position: absolute;
        right: 0;
        font-family: icon-orange, sans-serif;
        font-size: px-to-rem(12);
        color: $dark;
        text-decoration: none;
        content: $icon-arrow-next;
      }

      &:hover,
      &:focus {
        color: $black;

        .ob1-alert-title {
          text-decoration: underline;
        }
      }
    }

    @include media-breakpoint-up(md) {
      a {
        padding: px-to-rem(30);
      }

      .alert-icon {
        padding-right: px-to-rem(30);
      }

      .ob1-alert-title,
      .alert-content {
        padding-right: px-to-rem(38);
      }

      .alert-content {
        padding-left: px-to-rem(62);
      }
    }
  }

  .alert {
    padding: 0;
    margin-bottom: 0;

    &.ob1-alert-normal:not(:only-child) {
      font-weight: $font-weight-bold;
    }
  }

  .alert-content {
    padding-left: px-to-rem(47);

    .o-link-arrow {
      margin-top: px-to-rem(-1);
      margin-bottom: px-to-rem(-3);
    }

    .btn {
      margin-top: px-to-rem(15);
      margin-bottom: px-to-rem(12);
    }

    .alert-text {
      display: block;
      max-width: $text-max-width-xl;
      padding-top: px-to-rem(6);
      padding-bottom: px-to-rem(7);
      margin-bottom: px-to-rem(0);

      + .alert-text {
        padding-top: px-to-rem(8);
      }
    }

    .collapse,
    .collapsing {
      max-width: $text-max-width-xl;
    }

    .list {
      padding-top: px-to-rem(8);
      margin-bottom: px-to-rem(5);

      li {
        margin-left: 0;
      }

      + .o-link-arrow {
        margin-bottom: px-to-rem(5);
      }

      + .btn {
        margin-bottom: px-to-rem(20);
      }
    }
  }

  // Version réduite
  &-sm {
    padding-top: px-to-rem(8);
    padding-bottom: px-to-rem(0);

    .alert {
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;

      &:only-child {
        padding-bottom: px-to-rem(10);
      }

      .alert-icon {
        font-size: px-to-rem(20);
      }

      .ob1-alert-title {
        margin: 0;
      }
    }

    .alert-content {
      padding-left: px-to-rem(35);
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;

      .o-link-arrow {
        margin: 0;
      }

      .collapse {
        padding-bottom: px-to-rem(10);
      }
    }
  }

  &.additional-message {
    background-color: $gray2;
  }

  &.invalid-feedback,
  &.limit-reached-feedback {
    display: none;
    max-width: px-to-rem($medium-form-group-max-width);
    padding-left: px-to-rem(15) !important; // stylelint-disable-line declaration-no-important
    margin: 0;

    &::after {
      display: none;
    }
  }
}

@include media-breakpoint-up(md) {
  .alert-container {
    &.ob1-alert-row {
      flex-direction: row;
      justify-content: space-between;

      .alert-content {
        padding: 0 $spacer 0 ($spacer * 1.5);
        margin: auto 0;

        .btn {
          margin: px-to-rem(10) 0;
        }
      }
    }
  }
}

// deprecated
.alert-container:not(.additional-message) {
  .o-link-arrow::after,
  .o-link-arrow:hover,
  .o-link-arrow:focus {
    color: $black;
    text-decoration-color: $black;
  }

  .list li::before {
    background-color: $black;
  }
}

.alert {
  display: flex;
  align-items: center;
  font-size: $font-size-base;
  font-weight: $font-weight-bold;

  .alert-icon {
    position: static;
    margin-right: px-to-rem(15);
    font-family: icon-orange, sans-serif;
    font-size: $alert-icon-size;
    font-weight: $font-weight-normal;
    line-height: 1;
  }

  .ob1-alert-title {
    max-width: $text-max-width-xl;
    margin-top: 3px;
    margin-bottom: 7px;
  }

  &.ob1-alert-normal {
    font-weight: $font-weight-normal;
  }
}

@each $color, $value in $theme-colors {
  .alert-#{$color} {
    // Boosted mod adapt color with background, and keep initial brand color value for background
    @include alert-variant(theme-color-level($color, $alert-bg-level), theme-color-level($color, $alert-border-level), $black);
  }
}

.alert-success:not(.ob1-notification-message) .alert-icon {
  @extend .icon-tick-circle;

  &::before {
    content: $icon-tick-circle;
  }
}

.alert-danger:not(.ob1-notification-message) .alert-icon {
  @extend .icon-error-severe;

  &::before {
    content: $icon-error-severe;
  }
}

.alert-warning:not(.ob1-notification-message) .alert-icon {
  @extend .icon-Warning-important;

  &::before {
    content: $icon-Warning-important;
  }
}

.alert-info:not(.ob1-notification-message) .alert-icon {
  @extend .icon-info;

  &::before {
    content: $icon-info;
  }
}
