.o-switch {
  .labels {
    padding-right: px-to-rem(15);
    padding-left: px-to-rem(15);
  }

  .toggle {
    width: px-to-rem(78);
    height: auto;
    cursor: pointer;
    background: $gray4;
    border: 2px solid $gray4;
    outline-offset: px-to-rem(4);
    transition: outline-offset .15s ease-in-out;

    &::before {
      width: px-to-rem(28);
      height: px-to-rem(28);
      box-shadow: 0 1px 1px $black;
    }

    .off,
    .on {
      margin-top: px-to-rem(3);
      margin-bottom: px-to-rem(5);
      font-size: $font-size;
      line-height: px-to-rem(20);
      color: $black;
      cursor: pointer;
    }
  }

  .checkbox {
    &:checked + .toggle {
      background: $green;
      border-color: $green;
    }

    &.success:disabled + .toggle,
    &:disabled + .toggle {
      background: $gray4;
      border-color: $gray4;
    }

    &:disabled + .toggle {
      cursor: not-allowed;

      .on,
      .off {
        cursor: not-allowed;
      }
    }

    // stylelint-disable selector-max-class
    &.success:disabled + .toggle .on,
    &.success:disabled + .toggle .off,
    &:disabled + .toggle .on,
    &:disabled + .toggle .off { // stylelint-enable selector-max-class
      color: $white;
    }

    &:focus + .toggle {
      outline: px-to-rem(2) solid $primary;
      outline-offset: 0;
    }
  }

  &.ob1-switch-inverse {
    .checkbox {
      &:not(:checked) + .toggle {
        background: $gray3;
        border-color: $gray3;

        /* stylelint-disable-next-line selector-max-class */
        .off {
          color: $white;
        }
      }

      /* stylelint-disable-next-line selector-max-class */
      &.success:disabled + .toggle,
      &:disabled + .toggle {
        background: $gray3;
        border-color: $gray3;

        /* stylelint-disable-next-line selector-max-class */
        &::before {
          background-color: $black;
        }

        /* stylelint-disable-next-line selector-max-class */
        .on,
        .off {
          color: $black;
        }
      }

      &:focus + .toggle {
        outline-color: $primary-official;
      }
    }
  }
}
