.items-list {
  padding: 0;
  list-style: none;
  counter-reset: step;

  &:not(.row) {
    margin: 0;
  }

  &.ob1-items-list-inline {
    display: inline-flex;
    flex-flow: row wrap;

    .item-container {
      margin-right: ($spacer * 1.5);
    }
  }

  &.is-invalid ~ .invalid-feedback {
    display: flex;
  }

  // deprecated - start
  .alert {
    flex: 1 100%;
    max-width: $spacer * 15;
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;

    .alert-icon {
      height: px-to-rem(20);
      font-size: px-to-rem(20);
    }
  }
  // deprecated - end

  .form-control-help {
    max-width: px-to-rem($medium-form-group-max-width);
    padding-top: px-to-rem(8);
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;
    color: $gray3;
  }
}

.items-list-item:not(a):not(button) { // stylelint-disable-line selector-no-qualifying-type
  .item-container::after {
    display: none;
  }

  .item-text .item-desc,
  .item-text span {
    text-decoration: none !important; // stylelint-disable-line declaration-no-important
  }

  .item-text span:not(:last-of-type) {
    margin-bottom: $spacer * .5;
  }

  &:hover,
  &:active {
    .item-desc {
      color: $gray3 !important; // stylelint-disable-line declaration-no-important
      text-decoration: none;
    }

    .item-container,
    .item-text span,
    .item-desc-price,
    .item-icon {
      color: $black !important; // stylelint-disable-line declaration-no-important
    }

    .item-text {
      text-decoration: none;
    }
  }
}

button.items-list-item { // stylelint-disable-line selector-no-qualifying-type
  padding: 0;
  outline-offset: px-to-rem(4);
  transition: outline-offset .15s ease-in-out;

  &:focus {
    outline: px-to-rem(2) solid $primary;
    outline-offset: 0;
  }
}

.items-list-item {
  $itemlist-radio-margin: px-to-rem(15) !default;
  display: flex;
  align-items: center;
  width: 100%;
  max-width: px-to-rem(450);
  height: 100%;
  text-align: left;
  text-decoration: none;
  background: transparent;
  border: none;

  .ob1-item-step {
    color: $black;

    &::before {
      display: flex;
      align-items: center;
      justify-content: center;
      width: px-to-rem(32);
      height: px-to-rem(32);
      font-size: $font-size-transverse;
      line-height: 1.25;
      content: counter(step);
      counter-increment: step;
      background-color: $white;
      border: 1px solid;
      border-radius: 50%;
    }
  }

  .item-container {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    min-height: px-to-rem(70);
    padding: px-to-rem(8) 0 px-to-rem(12);
    border-bottom: 1px solid $gray4;

    &.item-container-switch {
      &::after {
        display: none;
      }

      .o-switch {
        margin-left: $spacer / 2;
      }
    }

    &.no-border-bottom {
      border-bottom: none;
    }

    .form-group {
      margin-bottom: 0;
    }

    .custom-checkbox,
    .custom-radio {
      width: px-to-rem(20);
      padding-left: 0;
      margin-right: $itemlist-radio-margin;
      margin-bottom: 0;
    }

    .item-text {
      display: flex;
      flex: 1;
      flex-direction: column;
      margin-bottom: 0;
      font-size: $font-size-transverse;
      font-weight: $font-weight-normal;

      span:last-child {
        display: flex;
        align-items: center;
        min-height: px-to-rem(45);
      }
    }

    &:not(.item-loading)::after {
      padding-top: px-to-rem(3);
      margin-left: px-to-rem(15);
      font-family: icon-orange, sans-serif;
      font-size: px-to-rem(12);
      color: $primary;
      content: $icon-arrow-next;
    }
  }

  .item-container-sm {
    min-height: px-to-rem(54);
    padding: px-to-rem(6) 0 px-to-rem(10);
    border-bottom: none;

    .item-text {
      span:last-child {
        min-height: px-to-rem(32);
      }
    }
  }

  .item-container-checkbox-long-text {
    align-items: flex-start;

    .custom-checkbox {
      margin-top: px-to-rem(14);
    }
  }

  .item-icon {
    margin-right: px-to-rem(15);
    font-size: px-to-rem(32);
    font-weight: 400;
  }

  .item-img {
    width: px-to-rem(32);
    height: auto;
    margin-right: px-to-rem(15);
  }

  .item-status {
    padding-top: px-to-rem(5);
    margin-left: px-to-rem(15);
    font-size: px-to-rem(24);
    font-weight: 400;

    &.item-status-text {
      max-width: calc(33% - #{px-to-rem(15)});
      color: $gray3;
      text-align: right;
    }
  }

  .item-status-text,
  .item-notification,
  .item-desc-price {
    font-size: $font-size-transverse2;
    font-weight: $font-weight-bold;
    line-height: $line-height-transverse2;
  }

  .item-notification {
    height: px-to-rem(22);
    padding-right: px-to-rem(7);
    padding-left: px-to-rem(7);
    margin-left: px-to-rem(15);
    color: $white;
    background-color: $danger;
    border-radius: px-to-rem(11);
  }

  .item-desc {
    margin-top: px-to-rem(3);
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;
    color: $gray3;
  }

  .item-desc-price {
    color: $black;
  }

  .capsule, // deprecated
  .ob1-capsule {
    margin-right: 0;
    margin-left: px-to-rem(15);
  }

  &:hover,
  &:focus {
    .item-container,
    .item-icon,
    .item-desc-price {
      color: $gray3;
    }

    .item-text {
      text-decoration: underline;
      text-decoration-color: $gray3;
    }
  }

  &:focus:not(:focus-visible):not(:hover) {
    .item-container,
    .item-icon,
    .item-desc-price {
      color: $black;
    }

    .item-text {
      text-decoration: none;
    }
  }

  &:active {
    .item-icon,
    .item-container,
    .item-desc {
      color: $primary;
    }

    .item-text {
      text-decoration: underline;
      text-decoration-color: $primary;
    }
  }

  &.disabled {
    color: $gray4 !important; // stylelint-disable-line declaration-no-important
    pointer-events: none;

    .item-container,
    .item-desc,
    .item-icon,
    .item-icon::before,
    .item-status,
    .item-text,
    .item-container::after {
      color: $gray4 !important; // stylelint-disable-line declaration-no-important
      text-decoration: none !important; // stylelint-disable-line declaration-no-important
    }
    .ob1-network-quality-single-indicator::after {
      background-color: $gray4;
    }
  }

  &.highlight {
    .item-icon,
    .item-icon::before {
      color: $primary !important;  // stylelint-disable-line declaration-no-important
    }

    .item-text span {
      font-weight: $font-weight-bold;
    }
    .ob1-network-quality-single-indicator span {
      background-color: $primary;
    }
  }

  &.items-list-item-large {
    max-width: px-to-rem(739); // 700px + icone + marges
  }

  &.is-invalid ~ .invalid-feedback {
    display: flex;
  }
}

.ob1-network-quality {
  margin-left: 11px;

  span {
    display: inline-block;
    width: px-to-rem(7);
    height: px-to-rem(7);
    margin-left: px-to-rem(4);
    background-color: $gray4;
    border-radius: 50%;
  }

  &.ob1-network-medium-low :first-child,
  &.ob1-network-medium-low :nth-child(2),
  &.ob1-network-medium-high :first-child,
  &.ob1-network-medium-high :nth-child(2),
  &.ob1-network-medium-high :nth-child(3) {
    background-color: $yellow;
  }

  &.ob1-network-bad :first-child {
    background-color: $red;
  }

  &.ob1-network-good span {
    background-color: $green;
  }
}

.ob1-network-quality-single-indicator {
  position: relative;

  &::after {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    width: px-to-rem(10);
    height: px-to-rem(10);
    content: "";
    background-color: $gray4;
    border-radius: 50%;
  }

  &.ob1-network-medium::after {
    background-color: $yellow;
  }

  &.ob1-network-bad::after {
    background-color: $red;
  }

  &.ob1-network-good::after {
    background-color: $green;
  }
}
