a {
  outline-offset: px-to-rem(4);
  transition: outline-offset .15s ease-in-out;

  &:focus {
    color: inherit;
    outline: px-to-rem(2) solid $primary;
    outline-offset: 0;
  }

  &:hover,
  &:active,
  &:focus {
    color: $gray3;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $black;
  }
}

.o-link-reverse {
  &:focus {
    outline-color: $primary-official;
  }
}

$o-link-arrow-color: $primary-official; // stylelint-disable-line scss/dollar-variable-default

.o-link {
  padding: px-to-rem(7) 0 px-to-rem(10);
  font-weight: $font-weight-bold;
  color: $black;
  text-decoration: underline;
  background-color: transparent;

  &:hover,
  &:focus {
    color: $gray3;
    text-decoration: underline;
    text-decoration-color: $gray3;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $black;
    text-decoration-color: $black;
  }

  &::after,
  &::before {
    text-decoration: underline;
  }

  &:active {
    color: $primary;
    text-decoration: underline;
    text-decoration-color: $primary;
  }

  &.disabled {
    color: $gray4 !important; // stylelint-disable-line declaration-no-important
    text-decoration: underline;
    text-decoration-color: $gray4 !important; // stylelint-disable-line declaration-no-important
    cursor: not-allowed;
  }

  &.no-decoration {
    text-decoration: none;
  }

  &-small {
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;
  }

  &.ob1-link-dark {
    &:active,
    &:hover,
    &:focus {
      color: $dark !important; // stylelint-disable-line declaration-no-important
      text-decoration-color: $dark !important; // stylelint-disable-line declaration-no-important
    }
  }

  &.o-link-reverse {
    color: $white;

    &:hover,
    &:focus {
      color: $gray4;
      text-decoration-color: $gray4;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $white;
      text-decoration-color: $white;
    }

    &:active {
      color: $primary-official;
      text-decoration-color: $primary-official;
    }

    &.disabled {
      color: $gray3 !important; // stylelint-disable-line declaration-no-important
      text-decoration-color: $gray3 !important; // stylelint-disable-line declaration-no-important
    }
  }
}

// stylelint-disable selector-no-qualifying-type
button.o-link {
  border: none;
}

button.o-link-arrow {
  text-align: left;
}

button.ob1-link-icon {
  padding-right: 0;
  padding-left: 0;
  text-align: left;
  background-color: transparent;
  border-color: transparent;
  border-width: 0;
  outline-offset: px-to-rem(4);
  transition: outline-offset .15s ease-in-out;

  &:focus {
    outline: px-to-rem(2) solid $primary;
    outline-offset: 0;
  }

  &:active {
    padding-left: 0;
  }
}
// stylelint-enable selector-no-qualifying-type

.o-link-arrow {
  display: block;
  max-width: px-to-rem(300);
  padding: px-to-rem(7) px-to-rem(18) px-to-rem(10) 0;
  font-weight: $font-weight-bold;
  color: $black;
  text-decoration: none;
  background-color: transparent;
  border: none;

  // Used by IE to override the property with 'none'
  &::after {
    text-decoration: underline;
  }

  &::after { // stylelint-disable-line no-duplicate-selectors
    position: absolute;
    padding: px-to-rem(5) 0 px-to-rem(3) px-to-rem(10);
    margin-top: px-to-rem(1);
    color: $primary;
    text-decoration: none;
    content: $icon-arrow-next;
  }

  &.o-link-small {
    padding: px-to-rem(8) 0 px-to-rem(10);
  }

  &.ob1-link-large {
    max-width: 450px;
  }

  &.o-link-small::after {
    padding-left: px-to-rem(7);
    font-size: px-to-rem(9);
  }

  &[aria-expanded="true"]::after {
    padding: px-to-rem(3) px-to-rem(10) px-to-rem(5) 0;
    transform: rotate(180deg);
  }

  &.o-link-small[aria-expanded="true"]::after {
    padding: 0 px-to-rem(7) px-to-rem(3) 0;
  }

  &::before {
    text-decoration: none;
  }

  &:hover,
  &:focus {
    color: $gray3;
    text-decoration: underline;
    text-decoration-color: $gray3;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $black;
    text-decoration: none;
  }

  &.back {
    position: relative;
    padding-right: 0;
    padding-left: px-to-rem(18);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    &::before {
      position: absolute;
      top: px-to-rem(12);
      left: 0;
      height: px-to-rem(16);
      color: $primary;
      text-decoration: none;
    }
  }

  &:active {
    color: $primary;
    text-decoration: underline;
    text-decoration-color: $primary;
  }

  &.no-decoration {
    text-decoration: none;
  }

  &.up {
    &::after {
      content: $icon-arrow-up;
    }
  }

  &.down {
    &::after {
      content: $icon-arrow-down;
    }
  }

  &.disabled {
    color: $gray4 !important; // stylelint-disable-line declaration-no-important
    text-decoration: none;
    cursor: not-allowed;

    &::after,
    &::before,
    &.back::after,
    &.back::before {
      color: $gray4 !important; // stylelint-disable-line declaration-no-important
    }
  }

  &.o-link-arrow-dark, // deprecated
  &.ob1-link-dark {
    color: $dark !important; // stylelint-disable-line declaration-no-important
    text-decoration-color: $dark !important; // stylelint-disable-line declaration-no-important

    &::after,
    &.back::before {
      color: $dark !important; // stylelint-disable-line declaration-no-important
    }

    &:active,
    &:hover,
    &:focus {
      color: $dark !important; // stylelint-disable-line declaration-no-important
      text-decoration-color: $dark !important; // stylelint-disable-line declaration-no-important
    }
  }

  &.o-link-reverse {
    color: $white;

    &::after,
    &.back::before {
      color: $primary-official;
    }

    &:hover,
    &:focus {
      color: $gray4;
      text-decoration-color: $gray4;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $white;
    }

    &:active {
      color: $primary-official;
      text-decoration-color: $primary-official;
    }

    &.disabled {
      color: $gray3 !important; // stylelint-disable-line declaration-no-important
      text-decoration-color: $gray3 !important; // stylelint-disable-line declaration-no-important

      &::after,
      &::before {
        color: $gray3 !important; // stylelint-disable-line declaration-no-important
      }
    }
  }
}

.ob1-link-icon {
  display: inline-flex;
  align-items: center;
  max-width: px-to-rem(300);
  padding-top: px-to-rem(7);
  padding-bottom: px-to-rem(10);

  &:hover,
  &:focus {
    color: $gray3;

    .ob1-link-icon-text {
      text-decoration: underline;
      text-decoration-color: $gray3;
    }
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $black;

    .ob1-link-icon-text {
      text-decoration: none;
    }
  }

  &:active,
  &.focus-within:hover {
    color: $primary;

    .ob1-link-icon-text {
      text-decoration-color: $primary;
    }
  }

  .icon {
    font-size: px-to-rem(20);
  }

  .ob1-link-icon-text {
    margin-left: px-to-rem(10);
    font-size: $font-size-transverse;
    font-weight: $font-weight-bold;
    line-height: $line-height-transverse;
  }

  &.o-link-reverse {
    color: $white;

    &:hover,
    &:focus {
      color: $gray4;

      .ob1-link-icon-text {
        text-decoration-color: $gray4;
      }
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $white;
    }

    &:active,
    &.focus-within:hover {
      color: $primary-official;

      .ob1-link-icon-text {
        text-decoration-color: $primary-official;
      }
    }

    &.ob1-disabled {
      color: $gray3 !important; // stylelint-disable-line declaration-no-important
    }
  }

  &.ob1-disabled {
    color: $gray4 !important; // stylelint-disable-line declaration-no-important
    cursor: not-allowed;

    .ob1-link-icon-text {
      text-decoration: none;
    }
  }

  &-small {
    .icon {
      font-size: px-to-rem(18);
    }

    .ob1-link-icon-text {
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;
    }
  }

  &.ob1-link-dark {
    &:hover,
    &:active,
    &:focus,
    .ob1-link-icon-text:hover,
    .ob1-link-icon-text:focus {
      color: $dark !important; // stylelint-disable-line declaration-no-important
      text-decoration-color: $dark !important; // stylelint-disable-line declaration-no-important
    }
  }
}
