@font-face {
  font-family: SoshMedium;
  font-style: normal;
  font-weight: $font-weight-base;
  src: url("#{$font-sosh-path}Sosh-Medium.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: SoshMedium;
  font-style: normal;
  font-weight: $font-weight-bold;
  src: url("#{$font-sosh-path}Sosh-Bold.woff") format("woff");
  font-display: swap;
}
