@mixin ob1-skeleton-animation($background-color, $skeleton-animation-color){
  position: relative;
  display: block;
  overflow: hidden;
  background-color: $background-color;

  &::after {
    position: absolute;
    top: 0;
    right: -25%;
    left: -25%;
    z-index: 1;
    height: 100%;
    content: "";
    background: linear-gradient(90deg, $background-color, $skeleton-animation-color, $background-color);
    transform: translateX(-100%);
    animation: ob1-skeleton-loading 1.25s infinite;
  }
}

@keyframes ob1-skeleton-loading {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(100%);
  }
}
