$font-size-base: .875rem; // 14px

$spacer: .625rem;
$spacers: (
  0: 0,
  1: ($spacer * .5),
  2: $spacer,
  3: ($spacer * 1.5),
  4: ($spacer * 2),
  5: ($spacer * 3)
);

// Logo
$logo-width: px-to-rem(30);
$logo-height: px-to-rem(30);

// Typos
$h1-font-size-outils-metiers:         px-to-rem(24);
$h1-line-height-outils-metiers:       (28 / 24);
$h2-font-size-outils-metiers:         px-to-rem(20);
$h2-line-height-outils-metiers:       (24 / 20);
$h3-font-size-outils-metiers:         px-to-rem(18);
$h3-line-height-outils-metiers:       (24 / 18);
$h4-font-size-outils-metiers:         px-to-rem(16);
$h4-line-height-outils-metiers:       (22 / 16);
$title-padding-top-outils-metiers:    px-to-rem(1);
$h1-padding-top-outils-metiers:       0;
$title-padding-bottom-outils-metiers: px-to-rem(5);
$h2-padding-bottom-outils-metiers:    px-to-rem(11);
$lead-font-size-outils-metiers:       px-to-rem(14);
$lead-line-height-outils-metiers:     (20 / 14);

$font-size-transverse:        px-to-rem(16);
$line-height-transverse:      (22 / 16);
$font-size-transverse2:       px-to-rem(14);
$line-height-transverse2:     (20 / 14);
$font-size-transverse3:       px-to-rem(12);
$line-height-transverse3:     (18 / 12);

/* Typography configuration for mobile (xs and sm) */
$h1-font-size-mobile:         $h1-font-size-outils-metiers;
$h1-line-height-mobile:       $h1-line-height-outils-metiers;
$h1-padding-top-mobile:       $h1-padding-top-outils-metiers;
$h1-padding-bottom-mobile:    $title-padding-bottom-outils-metiers;
$h2-font-size-mobile:         $h2-font-size-outils-metiers;
$h2-line-height-mobile:       $h2-line-height-outils-metiers;
$h2-padding-top-mobile:       $title-padding-top-outils-metiers;
$h2-padding-bottom-mobile:    $h2-padding-bottom-outils-metiers;
$h2-no-signature-padding-bottom-mobile: $title-padding-bottom-outils-metiers;
$h3-font-size-mobile:         $h3-font-size-outils-metiers;
$h3-line-height-mobile:       $h3-line-height-outils-metiers;
$h3-padding-top-mobile:       $title-padding-top-outils-metiers;
$h3-padding-bottom-mobile:    $title-padding-bottom-outils-metiers;
$h4-font-size-mobile:         $h4-font-size-outils-metiers;
$h4-line-height-mobile:       $h4-line-height-outils-metiers;
$h4-padding-top-mobile:       $title-padding-top-outils-metiers;
$h4-padding-bottom-mobile:    $title-padding-bottom-outils-metiers;
// La classe .lead n'est pas disponible pour cette déclinaison, on sécurise quand même en alignant la taille sur .standard
$lead-font-size-mobile:       $lead-font-size-outils-metiers;
$lead-line-height-mobile:     $lead-line-height-outils-metiers;

/* Typography configuration for tablet and small desktop (md, lg) */
$h1-font-size:         $h1-font-size-outils-metiers;
$h1-line-height:       $h1-line-height-outils-metiers;
$h1-padding-top:       $h1-padding-top-outils-metiers;
$h1-padding-bottom:    $title-padding-bottom-outils-metiers;
$h2-font-size:         $h2-font-size-outils-metiers;
$h2-line-height:       $h2-line-height-outils-metiers;
$h2-padding-top:       $title-padding-top-outils-metiers;
$h2-padding-bottom:    $h2-padding-bottom-outils-metiers;
$h2-no-signature-padding-bottom: $title-padding-bottom-outils-metiers;
$h3-font-size:         $h3-font-size-outils-metiers;
$h3-line-height:       $h3-line-height-outils-metiers;
$h3-padding-top:       $title-padding-top-outils-metiers;
$h3-padding-bottom:    $title-padding-bottom-outils-metiers;
$h4-font-size:         $h4-font-size-outils-metiers;
$h4-line-height:       $h4-line-height-outils-metiers;
$h4-padding-top:       $title-padding-top-outils-metiers;
$h4-padding-bottom:    $title-padding-bottom-outils-metiers;
$h5-font-size:         px-to-rem(14); // @todo : not defined in docs
$h6-font-size:         px-to-rem(14); // @todo : not defined in docs
// La classe .lead n'est pas disponible pour cette déclinaison, on sécurise quand même en alignant la taille sur .standard
$lead-font-size:       $lead-font-size-outils-metiers;
$lead-line-height:     $lead-line-height-outils-metiers;

/* Typography configuration for standards desktops and hightests desktop (xl, xxl) */
$font-size-widescreen:            px-to-rem(14);
$line-height-widescreen:          (20 / 14);
$h1-font-size-widescreen:         $h1-font-size-outils-metiers;
$h1-line-height-widescreen:       $h1-line-height-outils-metiers;
$h1-padding-top-widescreen:       $h1-padding-top-outils-metiers;
$h1-padding-bottom-widescreen:    $title-padding-bottom-outils-metiers;
$h2-font-size-widescreen:         $h2-font-size-outils-metiers;
$h2-line-height-widescreen:       $h2-line-height-outils-metiers;
$h2-padding-top-widescreen:       $title-padding-top-outils-metiers;
$h2-padding-bottom-widescreen:    $h2-padding-bottom-outils-metiers;
$h2-no-signature-padding-bottom-widescreen: $title-padding-bottom-outils-metiers;
$h3-font-size-widescreen:         $h3-font-size-outils-metiers;
$h3-line-height-widescreen:       $h3-line-height-outils-metiers;
$h3-padding-top-widescreen:       $title-padding-top-outils-metiers;
$h3-padding-bottom-widescreen:    $title-padding-bottom-outils-metiers;
$h4-font-size-widescreen:         $h4-font-size-outils-metiers;
$h4-line-height-widescreen:       $h4-line-height-outils-metiers;
$h4-padding-top-widescreen:       $title-padding-top-outils-metiers;
$h4-padding-bottom-widescreen:    $title-padding-bottom-outils-metiers;
$small-font-size-widescreen:      px-to-rem(12);
$small-line-height-widescreen:    (18 / 12);
// La classe .lead n'est pas disponible pour cette déclinaison, on sécurise quand même en alignant la taille sur .standard
$lead-font-size-widescreen:       $lead-font-size-outils-metiers;
$lead-line-height-widescreen:     $lead-line-height-outils-metiers;

// * typographie utilisée pour le drop down du page menu
$dropdown-font-size: px-to-rem(14);

// Boutons
$btn-min-height-lg: 3.75em; // 60px
$btn-padding-y:     .563rem; // 9px
$btn-padding-x:     1.875rem; // 30px
$btn-line-height:   1.25rem; // 20px
$btn-font-size:     .875rem; // 14px

// Message d'alerte
$alert-padding-y:     .625rem;
$alert-padding-x:     $alert-padding-y;
$alert-margin-bottom: px-to-rem(10);
$alert-icon-size:     px-to-rem(20);

// Dropdown
$dropdown-padding-y: 0;
$dropdown-item-padding-y: px-to-rem(10);

//Radio Button List
$itemlist-radio-margin: px-to-rem(10);

// Accordion
$accordion-icon-size: 24;
