// stylelint-disable selector-list-comma-newline-after

/**
 * Typography (text and headers)
 * Based on Boosted media queries logic, we have default style for md and lg, and a media queries for either mobile and widescreen
 * So properties defined outside of those media queries are probably intended for md an lg screens
 */

/* The followings declarations belongs to the orange rectangle at the end of level 2 headers : "signature" */

.no-signature {
  padding-bottom: $h2-no-signature-padding-bottom;
}

.h1::after,
.h3::after,
.h4::after,
.h5::after,
.h6::after,
.no-signature::after {
  display: none;
}

h2::after,
.h2::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: px-to-rem(30);
  height: px-to-rem(6);
  content: "";
  background: $primary-official;
}

/* underneath are the declarations for md and lg screens (no media queries) */
body, .standard {
  font-size: $font-size;
  line-height: $line-height-base;
}

p, .standard {
  padding-top: px-to-rem(8);
  padding-bottom: px-to-rem(10);
}

h1, .h1 {
  max-width: $title-max-width;
  padding-top: $h1-padding-top;
  padding-bottom: $h1-padding-bottom;
  line-height: $h1-line-height;
}

h2, .h2 {
  position: relative;
  z-index: 0;
  max-width: $title-max-width;
  padding-top: $h2-padding-top;
  padding-bottom: $h2-padding-bottom;
  line-height: $h2-line-height;
}

h3, .h3 {
  max-width: $title-max-width;
  padding-top: $h3-padding-top;
  padding-bottom: $h3-padding-bottom;
  line-height: $h3-line-height;
}

h4, .h4 {
  max-width: $text-max-width;
  padding-top: $h4-padding-top;
  padding-bottom: $h4-padding-bottom;
  line-height: $h4-line-height;
}

.lead {
  padding-top: px-to-rem(7);
  padding-bottom: px-to-rem(10);
  line-height: $lead-line-height;
}

.small {
  padding-top: px-to-rem(8);
  padding-bottom: px-to-rem(11);
  line-height: $small-line-height;
}

.text-mw {
  max-width: $text-max-width;
}

@include media-breakpoint-down(sm) {
  body, .standard {
    font-size: $font-size-mobile;
    line-height: $line-height-mobile;
  }
  p, .standard {
    padding-top: px-to-rem(8);
    padding-bottom: px-to-rem(10);
  }
  h1, .h1 {
    padding-top: $h1-padding-top-mobile;
    padding-bottom: $h1-padding-bottom-mobile;
    line-height: $h1-line-height-mobile;
  }
  h2, .h2 {
    padding-top: $h2-padding-top-mobile;
    padding-bottom: $h2-padding-bottom-mobile;
    line-height: $h2-line-height-mobile;
  }
  .no-signature {
    padding-bottom: $h2-no-signature-padding-bottom-mobile;
  }
  h3, .h3 {
    padding-top: $h3-padding-top-mobile;
    padding-bottom: $h3-padding-bottom-mobile;
    line-height: $h3-line-height-mobile;
  }
  h4, .h4 {
    padding-top: $h4-padding-top-mobile;
    padding-bottom: $h4-padding-bottom-mobile;
    line-height: $h4-line-height-mobile;
  }

  .lead {
    padding-top: px-to-rem(7);
    padding-bottom: px-to-rem(10);
    font-size: $lead-font-size-mobile;
    line-height: $lead-line-height-mobile;
  }

  .small {
    padding-top: px-to-rem(8);
    padding-bottom: px-to-rem(11);
    font-size: $small-font-size-mobile;
    line-height: $small-line-height-mobile;
  }
}

@include media-breakpoint-up(xl) {

  body, .standard {
    font-size: $font-size-widescreen;
    line-height: $line-height-widescreen;
  }
  p, .standard {
    padding-top: px-to-rem(7);
    padding-bottom: px-to-rem(10);
  }
  h1, .h1 {
    max-width: $title-max-width-xl;
    padding-top: $h1-padding-top-widescreen;
    padding-bottom: $h1-padding-bottom-widescreen;
    font-size: $h1-font-size-widescreen;
    line-height: $h1-line-height-widescreen;
  }
  h2, .h2 {
    max-width: $title-max-width-xl;
    padding-top: $h2-padding-top-widescreen;
    padding-bottom: $h2-padding-bottom-widescreen;
    font-size: $h2-font-size-widescreen;
    line-height: $h2-line-height-widescreen;
  }
  .no-signature {
    padding-bottom: $h2-no-signature-padding-bottom-widescreen;
  }
  h3, .h3 {
    max-width: $title-max-width-xl;
    padding-top: $h3-padding-top-widescreen;
    padding-bottom: $h3-padding-bottom-widescreen;
    font-size: $h3-font-size-widescreen;
    line-height: $h3-line-height-widescreen;
  }
  h4, .h4 {
    max-width: $text-max-width-xl;
    padding-top: $h4-padding-top-widescreen;
    padding-bottom: $h4-padding-bottom-widescreen;
    font-size: $h4-font-size-widescreen;
    line-height: $h4-line-height-widescreen;
  }
  h5, .h5 {
    font-size: $h5-font-size-widescreen;
  }
  h6, .h6 {
    font-size: $h6-font-size-widescreen;
  }
  .lead {
    padding-top: px-to-rem(6);
    padding-bottom: px-to-rem(10);
    font-size: $lead-font-size-widescreen;
    line-height: $lead-line-height-widescreen;
  }
  .small {
    padding-top: px-to-rem(8);
    padding-bottom: px-to-rem(10);
    font-size: $small-font-size-widescreen;
    line-height: $small-line-height-widescreen;
  }
  .text-mw:not(.small) {
    max-width: $text-max-width-xl;
  }
}
