.price, // deprecated
.ob1-price {
  $price-font-size: ($font-size-base * 2.125) !default; // 34px
  $price-line-height: (40 / 34) !default;
  $price-padding-top: 0 !default;
  $price-padding-bottom: px-to-rem(8) !default;
  $price-font-size-widescreen: ($font-size-base * 2.625) !default; // 42px
  $price-line-height-widescreen: (50 / 42) !default;
  $price-padding-top-widescreen: 0 !default;
  $price-padding-bottom-widescreen: px-to-rem(6) !default;

  $price-compact-font-size-mobile: ($font-size-base * 1.25) !default; // 20px
  $price-compact-line-height-mobile: (24 / 20) !default;
  $price-compact-font-size: ($font-size-base * 1.5) !default; // 24px
  $price-compact-line-height: (28 / 24) !default;
  $price-compact-font-size-widescreen: ($font-size-base * 1.875) !default; //30px
  $price-compact-line-height-widescreen: (34 / 30) !default;

  display: inline-block;
  color: $gray3;
  @extend .text-mw;

  p {
    margin: 0;
  }

  .price-from, // deprecated
  .ob1-price-from,
  .price-by-month, // deprecated
  .ob1-price-by-month {
    color: $black;
  }

  .price-from, // deprecated
  .ob1-price-from,
  .price-from-custom, // deprecated
  .ob1-price-from-custom {
    display: block;
    padding-bottom: 0;
  }

  .price-from, // deprecated
  .ob1-price-from,
  .price-from-custom, // deprecated
  .ob1-price-from-custom,
  .price-by-month, // deprecated
  .ob1-price-by-month,
  .price-by-month-custom, // deprecated
  .ob1-price-by-month-custom,
  .ob1-price-without-promo {
    @extend .lead;
  }

  .price-by-month, // deprecated
  .ob1-price-by-month,
  .price-by-month-custom, // deprecated
  .ob1-price-by-month-custom,
  .ob1-price-without-promo,
  .price-amount, // deprecated
  .ob1-price-amount,
  .price-amount-custom, // deprecated
  .ob1-price-amount-custom {
    font-weight: $font-weight-bold;
  }

  .ob1-price-without-promo,
  .price-promo-duration, // deprecated
  .ob1-price-promo-duration,
  .price-promo-duration-custom, // deprecated
  .ob1-price-promo-duration-custom {
    display: block;
  }

  .ob1-price-without-promo {
    text-decoration: line-through;
  }

  .price-promo-duration, // deprecated
  .ob1-price-promo-duration,
  .price-promo-duration-custom, // deprecated
  .ob1-price-promo-duration-custom {
    padding-top: 0;
    padding-bottom: 0;
  }

  .price-promo-duration:nth-child(4), // deprecated,
  .ob1-price-promo-duration:nth-child(4),
  .price-promo-duration-custom:nth-child(4), // deprecated
  .ob1-price-promo-duration-custom:nth-child(4) {
    padding-top: px-to-rem(8);
    padding-bottom: 0;
  }

  .price-promo-duration-by-month, // deprecated
  .ob1-price-promo-duration-by-month,
  .price-promo-duration-by-month-custom, // deprecated
  .ob1-price-promo-duration-by-month-custom {
    display: inline-block;
    padding-top: 0;
    padding-bottom: 0;
    font-weight: $font-weight-bold;
  }

  .price-amount, // deprecated
  .ob1-price-amount {
    color: $primary;
  }

  .ob1-price-amount-label {
    @extend .price-amount;
  }

  &.ob1-price-compact {
    font-size: $small-font-size-mobile;
    line-height: $small-line-height;

    .ob1-price-from,
    .ob1-price-from-custom,
    .ob1-price-by-month,
    .ob1-price-without-promo {
      @extend .standard;
    }

    .ob1-price-amount,
    .ob1-price-amount-custom {
      font-size: $price-compact-font-size-mobile;
      line-height: $price-compact-line-height-mobile;
    }
  }

  &.price-mini, // deprecated
  &.ob1-price-mini {
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;

    .price-amount, // deprecated
    .ob1-price-amount,
    .price-amount-custom, // deprecated
    .ob1-price-amount-custom {
      padding-bottom: px-to-rem(3);
    }

    .price-by-month, // deprecated
    .ob1-price-by-month,
    .price-by-month-custom, // deprecated
    .ob1-price-by-month-custom,
    .price-amount, // deprecated
    .ob1-price-amount,
    .price-amount-custom, // deprecated
    .ob1-price-amount-custom {
      font-size: $font-size-transverse;
      line-height: $line-height-transverse;
    }

    .price-by-month, // deprecated
    .ob1-price-by-month,
    .price-amount, // deprecated
    .ob1-price-amount {
      color: $black;
    }
  }

  &.ob1-price-inverse {
    color: $gray4;

    .price-from, // deprecated
    .ob1-price-from,
    .price-by-month, // deprecated
    .ob1-price-by-month {
      color: $white;
    }

    .price-amount, // deprecated
    .ob1-price-amount {
      color: $primary-official;
    }

    &.price-mini, // deprecated
    &.ob1-price-mini {
      .price-amount, // deprecated
      .ob1-price-amount {
        color: $white;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .ob1-price-select {
      text-align: center;

      button {
        position: absolute;
        bottom: $spacer;
      }
    }

    &.ob1-price-compact {
      .ob1-price-amount,
      .ob1-price-amount-custom {
        font-size: $price-compact-font-size;
        line-height: $price-compact-line-height;
      }
    }

  }

  @include media-breakpoint-down(lg) {
    .price-amount, // deprecated
    .ob1-price-amount,
    .price-amount-custom, // deprecated
    .ob1-price-amount-custom {
      padding-top: $price-padding-top;
      padding-bottom: $price-padding-bottom;
      font-size: $price-font-size;
      line-height: $price-line-height;
    }
  }

  @include media-breakpoint-up(xl) {
    .price-amount, // deprecated
    .ob1-price-amount,
    .price-amount-custom, // deprecated
    .ob1-price-amount-custom {
      padding-top: $price-padding-top-widescreen;
      padding-bottom: $price-padding-bottom-widescreen;
      font-size: $price-font-size-widescreen;
      line-height: $price-line-height-widescreen;
    }

    &.ob1-price-compact {
      font-size: $small-font-size-widescreen;
      line-height: $small-line-height-widescreen;
      .ob1-price-amount,
      .ob1-price-amount-custom {
        font-size: $price-compact-font-size-widescreen;
        line-height: $price-compact-line-height-widescreen;
      }
    }
  }
}
