.alert-container {
  padding: px-to-rem(8) px-to-rem(15) px-to-rem(15) px-to-rem(10);

  .alert-content {
    padding-left: px-to-rem(30);

    .o-link-arrow {
      padding: $spacer 0 0;
      margin: 0;
    }

    .btn {
      margin-top: $spacer;
      margin-bottom: 0;
    }

    .alert-text {
      padding-top: $spacer;
      padding-bottom: 0;
    }

    .list {
      padding-top: px-to-rem(15);
      margin-bottom: 0;

      + .o-link-arrow {
        padding: 0;
        margin: 0;
      }

      + .btn {
        margin: 0;
      }
    }
  }

  &-sm {
    padding-bottom: px-to-rem(0);
  }

  &.invalid-feedback,
  &.limit-reached-feedback {
    padding-left: px-to-rem(10) !important; // stylelint-disable-line declaration-no-important
  }
}

@include media-breakpoint-up(md) {
  .alert-container {
    &.ob1-alert-row {
      .alert-content {
        padding: 0 px-to-rem(30) 0 px-to-rem(45);

        .btn {
          margin: px-to-rem(17) 0 $spacer;
        }
      }
    }
  }
}

.alert {
  padding-top: px-to-rem(8);
  padding-right: px-to-rem(15);

  .alert-icon {
    margin-right: $spacer;
  }


  .ob1-alert-title {
    margin: 0;
  }
}
