.accordion {
  .card {
    background-color: transparent;
    border: 0;

    &.disabledCard { // stylelint-disable-line selector-class-pattern
      pointer-events: none;

      a,
      button {
        color: $gray4;
      }

      .accordion-icon {
        color: $gray4;
      }
    }
  }

  .card-body {
    padding: 0;
  }

  .card-header {
    background-color: transparent;

    // stylelint-disable selector-no-qualifying-type
    a,
    button {
      // display flex is to fix min-height bug in IE11
      display: flex;
      flex-direction: column;
      padding: 0;
      font-weight: 700;
      background: transparent;
      border-top: 1px solid $gray4;

      &::after {
        background-image: none;
      }

      &:hover,
      &:focus {
        .accordion-title {
          color: $gray3;
          text-decoration: underline;
        }
      }

      &:focus:not(:focus-visible):not(:hover) .accordion-title {
        color: $black;
        text-decoration: none;
      }

      &:active {
        .accordion-title {
          color: $primary;
          text-decoration: underline;
        }
      }

      &[aria-expanded="true"] {
        .accordion-icon::before {
          content: $icon-arrow-up;
        }
      }
    }

    button {
      outline-offset: px-to-rem(4);
      transition: outline-offset .15s ease-in-out;

      &:focus {
        outline: px-to-rem(2) solid $primary;
        outline-offset: 0;
      }

      @include hover-focus-active () {
        text-decoration: none;
      }
    }

    .item-icon {
      margin-right: px-to-rem(15);
      font-size: px-to-rem($accordion-icon-size);
    }
    // stylelint-enable selector-no-qualifying-type
  }

  .card:first-child .card-header {
    a,
    button {
      border-top-color: transparent;
    }
  }

  .card:last-child .card-header {
    a,
    button {
      border-bottom: 1px solid $gray4;
    }
  }

  // stylelint-disable selector-max-compound-selectors,selector-max-class
  .card.card-expanded .card-header,
  .card.card-expanded + .card .card-header {
    a,
    button {
      border-top-color: $black;
    }
  }
  // stylelint-disable selector-no-qualifying-type
  .card.card-expanded {
    [aria-expanded="true"],
    + a,
    + button {
      border-top-color: $black;
    }
  }
  // stylelint-enable selector-no-qualifying-type

  .card.card-expanded:last-child {
    border-bottom: 1px solid $black;

    .card-header a,
    .card-header button {
      border-bottom-color: transparent;
    }
  }

  .card.card-expanded.multi:last-child {
    border-bottom: 1px solid $black;

    .card-header a,
    .card-header button {
      border-bottom: 0;
    }
  }
  // stylelint-enable selector-max-compound-selectors,selector-max-class

  .accordion-card-title {
    display: flex;
    align-items: center;
    width: 100%; // edge bug
    min-height: px-to-rem(70);

    .accordion-title {
      max-width: px-to-rem(600);
      margin-right: auto;
      font-size: $lead-font-size-mobile;
      line-height: $lead-line-height-mobile;
    }

    .accordion-icon {
      margin-right: px-to-rem(4);
      margin-left: px-to-rem(15);
      font-weight: $font-weight-normal;
      color: $primary;

      &::before {
        font-family: icon-orange, sans-serif;
        font-size: px-to-rem(12);
        content: $icon-arrow-down;
      }
    }
  }

  .ob1-accordion-large {
    .accordion-title {
      font-size: $h4-font-size-mobile;
      line-height: $h4-line-height-mobile;
    }

    .accordion-icon::before {
      font-size: px-to-rem(15);
    }
  }

  .accordion-card-body {
    max-width: px-to-rem(600);
    margin-bottom: px-to-rem(15);
  }
}

// version inverse sur fond noir
.ob1-accordion-inverse {
  color: $white;

  .card-header {
    a,
    button {
      color: $white;
      border-top-color: $gray3;

      &:hover {
        color: $gray4;
      }

      &:active {
        color: $orange1;
      }

      &:focus:not(:focus-visible):not(:hover) {
        color: $white;
        .accordion-title {
          color: $white;
        }
      }
    }
  }

  .card:last-child .card-header {
    a,
    button {
      border-bottom-color: $gray3;
    }
  }

  // stylelint-disable selector-max-compound-selectors,selector-max-class
  .card.card-expanded .card-header,
  .card.card-expanded + .card .card-header {
    a,
    button {
      border-top-color: $white;
    }
  }
  .card.multi.card-expanded:last-child,
  .card.card-expanded:last-child {
    border-bottom-color: $white;
  }
  // stylelint-enable selector-max-compound-selectors,selector-max-class
}

@include media-breakpoint-between(md, xl) {
  .accordion {
    .accordion-card-title {
      .accordion-title {
        font-size: $lead-font-size;
        line-height: $lead-line-height;
      }
    }

    .ob1-accordion-large {
      min-height: px-to-rem(80);

      .card-header .item-icon {
        font-size: px-to-rem(42);
      }

      .accordion-title {
        font-size: $h4-font-size;
        line-height: $h4-line-height;
      }

      .accordion-icon::before {
        font-size: px-to-rem(16);
      }
    }

    .accordion-card-body.lead {
      margin-bottom: px-to-rem(30);
    }
  }
}

@include media-breakpoint-up(xl) {
  .accordion {
    .accordion-card-title {
      .accordion-title {
        max-width: px-to-rem(700);
        font-size: $lead-font-size-widescreen;
        line-height: $lead-line-height-widescreen;
      }

      .accordion-icon::before {
        font-size: px-to-rem(14);
      }
    }

    .ob1-accordion-large {
      min-height: px-to-rem(90);

      .card-header .item-icon {
        font-size: px-to-rem(52);
      }

      .accordion-title {
        font-size: $h4-font-size-widescreen;
        line-height: $h4-line-height-widescreen;
      }

      .accordion-icon::before {
        font-size: px-to-rem(19);
      }
    }

    .accordion-card-body {
      max-width: px-to-rem(700);
      margin-bottom: px-to-rem(30);
    }
  }
}
