.ob1-menu-page {
  height: px-to-rem(52);
  .o-nav-light > .nav-item > .nav-link {
    padding-bottom: px-to-rem(15);
    font-size: px-to-rem(16);
    line-height: px-to-rem(22);
  }

  .o-nav-light > .nav-item:not(:first-child) > .nav-link {
    margin-left: px-to-rem(30);
  }

  &.ob1-menu-page-mini {
    height: px-to-rem(36);
    .nav-item > .nav-link {
      padding-top: px-to-rem(8);
      padding-bottom: px-to-rem(8);
      font-size: px-to-rem(14);
      line-height: px-to-rem(20);
    }

    .nav-item:not(:first-child) > .nav-link {
      margin-left: px-to-rem(20);
    }


    .nav-item {
      .nav-link::before {
        border-bottom-width: px-to-rem(2);
      }
    }
  }
}

