.btn {
  max-width: $btn-max-width;
  white-space: normal;
  outline-offset: px-to-rem(4);
  transition: outline-offset .15s ease-in-out;

  &:focus {
    outline: px-to-rem(2) solid $primary;
    outline-offset: 0;
  }

  @include hover-focus-active () {
    text-decoration: none;
  }

  &[data-ob1-component="ProgressBarDetermined"] {
    position: relative;
    color: $gray4 !important; // stylelint-disable-line declaration-no-important
    background-color: transparent !important; // stylelint-disable-line declaration-no-important
    border-color: currentcolor !important; // stylelint-disable-line declaration-no-important

    .ob1-progress-bar-determined {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }

    &.btn-inverse {
      color: $gray3 !important; // stylelint-disable-line declaration-no-important

      &.btn-primary:disabled {
        color: $gray3;
        background-color: transparent;
        border-color: currentcolor;
      }

      .ob1-progress-bar-determined {
        background-color: $gray3;
      }
    }
  }

  &:disabled,
  &.disabled {
    cursor: not-allowed;
  }

  &.btn-inverse {
    &:focus {
      outline-color: $primary-official;
    }
  }
}

.btn-lg {
  min-height: $btn-min-height-lg;
}

.btn-primary,
.btn-warning,
.btn-dark { // deprecared
  @include ob1-button-variant($black, $black, $gray3, $gray3, $primary, $primary, $gray4, $gray4, $white);

  &.btn-inverse {
    @include ob1-button-variant($white, $white, $gray4, $gray4, $primary, $primary, $gray3, $gray3, $black);
  }
}

.btn-secondary,
.btn-tertiary,
.btn-light {
  @include ob1-outline-button-variant(transparent, $black, transparent, $gray3, transparent, $primary, transparent, $gray4, $gray4, $black);

  &.btn-inverse {
    @include ob1-outline-button-variant(transparent, $white, transparent, $gray4, transparent, $primary2, transparent, $gray3, $gray3, $white);
  }
}
