.list {
  padding-left: 0;
  list-style-type: none;

  li {
    position: relative;
    max-width: px-to-rem(617); // la largeur maximale doit être de 600px (on doit rajouter les dimensions de padding ici)
    padding: px-to-rem(8) 0 px-to-rem(10) px-to-rem(17);
    margin-left: px-to-rem(10);
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;

    &::before {
      position: absolute;
      top: px-to-rem(16);
      left: 0;
      display: block;
      width: px-to-rem(7);
      height: px-to-rem(7);
      content: "";
      background-color: $primary;
      border-radius: 50%;
    }
  }

  &.ob1-list-tick {
    li {
      max-width: px-to-rem(624);  // la largeur maximale doit être de 600px (on doit rajouter les dimensions de padding ici)
      padding-left: px-to-rem(24);

      &::before {
        top: px-to-rem(10);
        width: auto;
        height: auto;
        font-family: $icomoon-font-family;
        font-size: $font-size;
        color: $primary;
        content: $icon-tick;
        background-color: transparent;
      }
    }
  }
}

.bg-success-light,
.bg-warning-light,
.bg-danger-light,
.bg-info-light {
  .list li::before {
    background-color: $black;
  }

  .ob1-list-tick li::before {
    color: $black;
    background-color: transparent;
  }
}

@include media-breakpoint-up(xl) {
  .list {
    li {
      max-width: px-to-rem(724); // la largeur maximale doit être de 700px (on doit rajouter les dimensions de padding ici)
      padding-top: px-to-rem(7);
      padding-left: px-to-rem(24);
      margin-left: px-to-rem(15);
      font-size: $font-size-transverse;
      line-height: $line-height-transverse;

      &::before {
        top: px-to-rem(15);
        width: px-to-rem(9);
        height: px-to-rem(9);
      }
    }

    &.ob1-list-tick {
      li {
        max-width: px-to-rem(731); // la largeur maximale doit être de 700px (on doit rajouter les dimensions de padding ici)
        padding-left: px-to-rem(31);

        &::before {
          font-size: $font-size-transverse;
        }
      }
    }
  }
}
