.custom-radio {
  $custom-radio-icon-size: 20px !default;

  padding-left: px-to-rem(35);

  .custom-control-label {
    padding-top: 0 !important; // stylelint-disable-line declaration-no-important
    line-height: px-to-rem(22) !important; // stylelint-disable-line declaration-no-important
  }

  .custom-control-input,
  .custom-control-input:active {
    ~ .custom-control-label::before {
      width: $custom-radio-icon-size;
      height: $custom-radio-icon-size;
      font-size: $custom-radio-icon-size !important; // stylelint-disable-line declaration-no-important
      line-height: $custom-radio-icon-size;
      color: $black;
      content: $icon-radio-button-non;
      background-color: transparent;
    }

    &:checked {
      ~ .custom-control-label::before {
        color: $primary;
        content: $icon-radio-button-oui-bold;
        background-color: transparent;
      }
    }

    &:hover,
    &:focus {
      ~ .custom-control-label::before {
        color: $gray3;
      }
    }

    &:focus:not(:focus-visible):not(:hover) {
      ~ .custom-control-label::before {
        color: $primary;
      }
    }

    &:active:not(:checked):not(:disabled) {
      ~ .custom-control-label::before {
        content: $icon-radio-button-non-bold;
      }
    }

    &:disabled {
      ~ .custom-control-label,
      ~ .custom-control-label::before {
        color: $gray4;
        cursor: not-allowed;
      }
    }
  }

  .custom-control-input {
    &:checked {
      ~ .custom-control-label::after {
        border: none;
      }
    }
  }

  &.ob1-custom-radio-inverse {
    .custom-control-input,
    .custom-control-input:active {
      ~ .custom-control-label::before {
        color: $white;
        background-color: transparent;
      }

      &:checked {
        ~ .custom-control-label::before {
          color: $primary2;
        }
      }

      &:hover,
      &:focus {
        ~ .custom-control-label::before {
          color: $gray4;
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        ~ .custom-control-label::before {
          color: $primary2;
        }
      }

      &:disabled {
        ~ .custom-control-label,
        ~ .custom-control-label::before {
          color: $gray3;
        }
      }
    }
  }
}
