.feedback {
  margin-bottom: ($spacer * 1.5);

  .alert {
    height: 100%;
    max-height: px-to-rem(218);
    padding: $spacer;
    margin-bottom: 0;

    .alert-icon {
      margin-right: auto;
      margin-left: auto;
      font-size: $feedback-icon-size;
    }
  }

  .feedback-title {
    @extend .h4;
    max-width: $text-max-width;
    margin: $spacer 0 0 0;
  }

  .feedback-text {
    @extend .lead;
    @extend .text-mw;
    max-width: $text-max-width;
    margin-bottom: $spacer;
  }

  .feedback-button {
    text-align: center;
  }
}

.modal-feedback {
  .modal-content {
    .modal-header {
      .o-link-arrow {
        @extend .o-link-arrow-dark;
      }
    }

    .feedback {
      .alert {
        max-height: 100%;
        padding: $spacer * 1.5;
      }
    }
  }
}

@include media-breakpoint-up(md) {
  .feedback {
    .feedback-title {
      margin-top: 0;
    }

    .feedback-button {
      text-align: left;
    }
  }
}

@include media-breakpoint-up(xl) {
  .feedback {
    .alert {
      max-height: px-to-rem(270);
    }
  }
}
