.site-width {
  max-width: $site-max-width;
  margin: auto;
}

.site-height {
  min-height: $site-height-xs;
  margin-bottom: $spacer;
}

@include media-breakpoint-up(sm) {
  .site-height {
    min-height: $site-height-sm;
  }
}

@include media-breakpoint-up(md) {
  .site-height {
    min-height: $site-height-md;
    margin-bottom: ($spacer * 1.5);
  }
}

@include media-breakpoint-up(lg) {
  .site-height {
    min-height: $site-height-lg;
  }
}

@include media-breakpoint-up(xl) {
  .site-height {
    min-height: $site-height-xl;
    margin-bottom: ($spacer * 2);
  }
}

@include media-breakpoint-up(xxl) {
  .site-height {
    min-height: $site-height-xxl;
  }
}

.custom-control {
  .custom-control-label {
    line-height: px-to-rem(22);

    &:active::before,
    &::before {
      width: px-to-rem(20);
      height: px-to-rem(20);
      font-family: icon-orange, sans-serif;
      font-size: px-to-rem(20);
      font-weight: $font-weight-normal;
      line-height: px-to-rem(20);
      color: $black;
      pointer-events: auto;
      cursor: pointer;
      background-color: $white;
      border: 0;
    }

    &::after {
      width: 0;
      height: 0;
      background-image: none;
      border: 0;
    }
  }

  .custom-control-input {
    ~ .custom-control-label::before {
      outline-offset: px-to-rem(4);
      transition: outline-offset .15s ease-in-out;
    }

    &:focus ~ .custom-control-label::before {
      outline: px-to-rem(2) solid $primary;
      outline-offset: 0;
    }
  }
}

.bg-dark,
.bg-gray1 {
  .custom-control {
    .custom-control-input {
      &:focus ~ .custom-control-label::before {
        outline-color: $primary2;
      }
    }
  }
}

/* Greige should be available only for background */
.bg-greige {
  background-color: $greige !important; // stylelint-disable-line declaration-no-important
}

