.tag, // deprecated
.ob1-tag {
  display: inline-block;
  padding: px-to-rem(2) px-to-rem(10) px-to-rem(4);
  font-size: $font-size-transverse2;
  font-weight: $font-weight-bold;
  line-height: $line-height-transverse2;
  color: $black;

  &.tag-primary, // deprecated
  &.ob1-tag-primary {
    background-color: $warning;
  }

  &.tag-subscribed, // deprecated
  &.ob1-tag-subscribed {
    position: relative;
    padding: px-to-rem(2) px-to-rem(10) px-to-rem(4) px-to-rem(26);
    color: $white;
    background-color: $black;

    &::before {
      position: absolute;
      top: px-to-rem(4);
      left: px-to-rem(8);
      font-family: icon-orange, sans-serif;
      font-size: px-to-rem(12);
      content: $icon-tick;
    }
  }

  &.tag-unavailable, // deprecated
  &.ob1-tag-unavailable {
    color: $black;
    background-color: $gray4;
  }

  &.tag-limited, // deprecated
  &.ob1-tag-limited {
    @extend .ob1-tag-primary;
  }

  &.tag-inverse, // deprecated
  &.ob1-tag-inverse {
    &.tag-subscribed, // deprecated
    &.ob1-tag-subscribed {
      color: $black;
      background-color: $white;
    }

    &.tag-unavailable, // deprecated
    &.ob1-tag-unavailable {
      color: $white;
      background-color: $gray3;
    }
  }
}
