/**
 * pour éviter d'avoir des couleurs non autorisées dans la charte, on redéfinit toutes les couleurs non autorisées
 * @see page 8 de l'UI kit OneI
 */
@each $color, $value in $theme-colors {
  @if index($color-allowed, $value) == null {
    //do something
    @include text-emphasis-variant(".text-#{$color}", $black);
  }
}

/**
 * seules les icones fonctionnelles peuvent être sur les couleurs autre que le noire, orange et grise
 * @see icones fonctionnelles de l'UI kit OneI
 */
@each $icon, $color in $functional-icons {
  .icon-#{$icon} {
    // stylelint-disable declaration-no-important
    color: $color !important;
    // stylelint-enable declaration-no-important
  }
}

/**
 * On surcharge le style par défaut qui avait une couleur rouge
 */
// stylelint-disable selector-no-qualifying-type, declaration-no-important
a.text-primary:hover,
a.text-primary:focus {
  color: $primary !important;
}
// stylelint-enable selector-no-qualifying-type, declaration-no-important
