$logo-width: px-to-rem(50) !default;
$logo-height: px-to-rem(50) !default;
$logo-small-width: px-to-rem(30) !default;
$logo-small-height: px-to-rem(30) !default;
$logo-label-max-width: px-to-rem(100) !default;

.ob1-logo {
  width: $logo-width;
  height: $logo-height;
}

.ob1-small-logo {
  width: $logo-small-width;
  height: $logo-small-height;
}

.ob1-logo-with-label {
  display: flex;
  align-items: flex-end;
  .ob1-logo-label {
    max-width: $logo-label-max-width;
    margin-bottom: px-to-rem(-4);
    margin-left: px-to-rem(10);
    font-weight: $font-weight-bold;
  }
}
