.dropdown-menu {
  &[x-placement="top-start"] {
    padding-bottom: px-to-rem(5);
    border-bottom-width: 0;
  }

  &[x-placement="bottom-start"] {
    padding-top: px-to-rem(5);
    border-top-width: 0;
  }
  .dropdown-header {
    padding-right: px-to-rem(30);
  }
}

.dropdown-item {
  padding-right: px-to-rem(30);
}

.ob1-dropdown-item-color-chip {
  display: flex;
  .ob1-color-chip {
    width: px-to-rem(20);
    height: px-to-rem(20);
    border: px-to-rem(1) $gray4 solid;
    border-radius: 50%;
  }
  .ob1-color-chip-label {
    margin-left: px-to-rem(10);
  }
}
