.o-switch .toggle,
.o-switch .toggle::before,
.o-switch .toggle .on,
.o-switch .toggle .off {
  transition: .4s;
}

.o-switch .toggle {
  position: relative;
  display: block;
  width: auto;
  min-width: 3.75rem;
  height: 1.875rem;
  margin: .3125rem 0;
  background: #666;
  border: 2px solid #666;
}

.o-switch .toggle.toggle-rounded, .o-switch .toggle.toggle-rounded::before {
  border-radius: 1.625rem;
}

.o-switch .toggle .on,
.o-switch .toggle .off {
  display: none;
  font-weight: 700;
  line-height: 1.625rem;
  color: #fff;
  text-align: left;
  cursor: default;
  opacity: 0;
}

.o-switch .toggle .on.svg-checkbox-tick::after {
  top: .1875rem;
  left: -.125rem;
}

.o-switch .toggle .off {
  display: block;
  text-align: right;
  opacity: 1;
}

.o-switch .toggle .off.svg-delete::after {
  top: .1875rem;
  right: -.125rem;
  left: auto;
}

.o-switch .toggle::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  width: 1.625rem;
  height: 1.625rem;
  content: "";
  background: #fff;
}

.o-switch .checkbox:focus + .toggle::before {
  outline: 1px dotted #fff;
}

.o-switch .checkbox:checked + .toggle {
  background: #f16e00;
  border-color: #f16e00;
}

.o-switch .checkbox:checked + .toggle .on {
  display: block;
  opacity: 1;
}

