$large-form-group-max-width: 600 !default;
$small-form-group-max-width: 260 !default;

.form-data {
  margin-bottom: ($spacer * .5);

  .ob1-filled-label {
    max-width: px-to-rem($medium-form-group-max-width);
    padding: 0;
    margin: 0;
    font-size: $font-size-mobile;
    color: $gray3;
  }

  .ob1-filled-data {
    max-width: px-to-rem($medium-form-group-max-width);
    padding: px-to-rem(3) 0;
    margin: 0;
  }
}

.form-group {
  position: relative;
  margin: 0 0 ($spacer * .5) 0;
  border: 1px solid transparent;

  .form-control[type="text"],
  .form-control[type="password"],
  .form-control[type="email"],
  .form-control[type="search"] {
    max-width: px-to-rem($medium-form-group-max-width);
    background-color: transparent;

    ~ .form-control-placeholder,
    ~ .invalid-feedback,
    ~ .form-control-help {
      max-width: px-to-rem($medium-form-group-max-width);
    }
  }

  .form-control[type="date"],
  .form-control[type="tel"],
  .form-control[type="text"].form-crypto, // deprecated
  .form-control.ob1-form-control-small {
    max-width: px-to-rem($small-form-group-max-width);

    ~ .form-control-placeholder,
    ~ .invalid-feedback,
    ~ .form-control-help {
      max-width: px-to-rem($small-form-group-max-width);
    }
  }

  // stylelint-disable selector-no-qualifying-type
  .form-control[type="search"],
  .form-control[type="text"] {
    /* Supprime la croix sur Internet Explorer */
    &::-ms-clear {
      display: none;
      width: 0;
      height: 0;
    }

    /* Supprime la croix sur Chrome */
    &::-webkit-search-cancel-button {
      display: none;
    }
  }

  &.form-group-inverse {
    input.form-control:not([size]):not([multiple]) {
      background-color: transparent;
    }

    select.form-control:not([size]):not([multiple]) {
      background-color: $black;
    }

    select.form-control,
    input.form-control {
      border-color: $gray3;

      &:not([size]):not([multiple]),
      ~ .form-control-placeholder,
      ~ .form-control-help {
        color: $white;
      }

      &:not(.form-control-empty),
      &:focus,
      &:active {
        border-color: $white;
      }

      &.is-invalid {
        border-bottom-color: $red;

        ~ .form-control-placeholder { // stylelint-disable-line selector-max-class
          color: $red;
        }
      }

      &:disabled {
        color: $gray3 !important; // stylelint-disable-line declaration-no-important
        border-color: $gray3 !important; // stylelint-disable-line declaration-no-important

        ~ .form-control-placeholder,
        &:hover ~ .form-control-placeholder,
        ~ .form-control-placeholder::after,
        ~ .form-control-help {
          color: $gray3 !important; // stylelint-disable-line declaration-no-important
        }
      }
    }
  }
  // stylelint-enable selector-no-qualifying-type
}

.form-control {
  display: inline-block;
  height: px-to-rem(70);
  padding: px-to-rem(35) px-to-rem(15) px-to-rem(13) 0;
  font-size: $font-size-transverse;
  font-weight: $font-weight-normal;
  line-height: $line-height-transverse;
  text-overflow: ellipsis;
  border: transparent;
  border-bottom: 1px solid $gray3;

  ~ .form-control-placeholder {
    position: absolute;
    top: 0;
    display: block;
    width: 100%;
    padding: px-to-rem(35) px-to-rem(54) px-to-rem(13) 0;
    margin: 0;
    overflow: hidden;
    font-size: $font-size-transverse;
    font-weight: $font-weight-normal;
    line-height: $line-height-transverse;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    transition: all 200ms;

    + .icon {
      display: inline-block;
      margin-top: px-to-rem(-38);
      margin-left: px-to-rem(-40);
      font-size: 1.5em;
      color: $primary;
    }

    // deprecated - start
    ~ .invalid-feedback:not(.alert-container),
    ~ .limit-reached-feedback.alert-warning {
      min-height: px-to-rem(38);
      padding-top: px-to-rem(8);
      padding-left: px-to-rem(50);
      margin: 0;
      font-size: $font-size-transverse2;
      font-weight: $font-weight-bold;
      color: $black;

      &::after {
        position: absolute;
        top: 50%;
        left: px-to-rem(15);
        width: auto;
        height: auto;
        font-family: icon-orange, sans-serif;
        font-size: px-to-rem(20);
        color: $red;
        content: $icon-error-severe;
        background-size: auto;
        transform: translateY(-50%);
      }
    }

    ~ .limit-reached-feedback.alert-warning {
      position: relative;
      display: none;
      padding-right: px-to-rem(15);

      &::after {
        color: $yellow;
        content: $icon-Warning-important;
      }
    }
    // deprecated - end
  }

  ~ .form-control-help,
  ~ .limit-reached-feedback {
    flex-direction: row;
    padding-top: px-to-rem(8);
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;
  }

  ~ .form-control-help {
    display: flex;
    flex-direction: column;
    color: $gray3;

    &.form-control-help-icon {
      flex-direction: row;
      color: $black;
    }
  }

  ~ .form-control-help .ob1-alert-title {
    color: $black;
  }

  &.form-control-empty ~ .form-control-placeholder {
    color: $gray3;
  }

  &.form-control-icon {
    padding-right: px-to-rem(54);

    ~ .form-control-placeholder + .btn-icon {
      position: relative;
      z-index: 1;
      padding: px-to-rem(1) 0 0 px-to-rem(1);
      margin-left: px-to-rem(-44);
      font-size: 1.5rem;
      color: $primary;
    }

    ~ .form-control-placeholder + .btn-clear {
      margin-left: px-to-rem(-32);
      font-size: .75rem;
    }

    &:focus ~ .form-control-placeholder + .btn-icon {
      margin-bottom: 1px; // supprime le saut de l'icone au focus, lorsque la bordure de l'input s'épaissit
    }

    &.form-control-empty:focus ~ .form-control-placeholder + .btn-icon { // stylelint-disable-line selector-max-class
      margin-bottom: 2px; // supprime le saut de l'icone au focus, lorsque la bordure de l'input s'épaissit
    }
  }

  ~ .ob1-spinner-undetermined {
    width: px-to-rem(24);
    height: px-to-rem(24);
    margin-left: px-to-rem(-40);
  }

  ~ .form-control-placeholder + .btn-icon,
  ~ .ob1-spinner-undetermined {
    &.ob1-search-field-icon-absolute { // stylelint-disable-line selector-max-class
      position: absolute;
      top: px-to-rem(34);
      right: px-to-rem(15);
      padding: 0;
      margin: 0;
    }
  }

  // deprecated - start
  &.show-password + .form-control-placeholder + .icon,
  &.hide-password + .form-control-placeholder + .icon {
    font-family: icon-orange, sans-serif;
    font-size: px-to-rem(24);
  }

  &.hide-password + .form-control-placeholder + .icon::before {
    content: $icon-Night-vision;
  }

  &.show-password + .form-control-placeholder + .icon::before {
    content: $icon-Hide;
  }
  // deprecated - end

  &.is-invalid {
    border-bottom: 2px solid $red;

    ~ .form-control-placeholder {
      font-weight: $font-weight-bold;
      color: $red;

      &::after,
      + .btn-icon,
      + .icon {
        color: $red;
      }
    }

    ~ .invalid-feedback {
      position: relative;
      display: block;
    }
  }

  &.limit-reached:focus {
    border-bottom: 2px solid $red;

    ~ .form-control-help {
      display: none;
    }

    ~ .limit-reached-feedback {
      display: flex;
    }
  }

  &:not(.form-control-empty),
  &:focus {
    height: px-to-rem(38);
    padding-top: px-to-rem(3);
    padding-bottom: px-to-rem(13);
    margin-top: px-to-rem(32);

    ~ .form-control-placeholder {
      height: px-to-rem(32);
      padding-top: px-to-rem(12);
      padding-bottom: 0;
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;
      cursor: auto;
      border-bottom: none;
      transform: translate3d(0, 0, 0);
    }
  }

  &:focus {
    padding-bottom: px-to-rem(12);
    border-bottom-width: 2px;
  }

  // cache le picto sur le textarea, ne doit pas impacter le chevron du select
  &:not(select):not(.form-control-empty),
  &:not(select):focus {
    ~ .form-control-placeholder::after {
      content: none;
    }
  }

  &::placeholder {
    opacity: 0;
  }

  &:hover:not(.is-invalid),
  &:focus:not(.is-invalid) {
    border-color: $black;
  }

  &:hover:not(.is-invalid) ~ .form-control-placeholder,
  &:focus:not(.is-invalid) ~ .form-control-placeholder {
    color: $black;
  }

  &:disabled {
    color: $gray4 !important; // stylelint-disable-line declaration-no-important
    background-color: transparent;
    border-color: $gray4 !important; // stylelint-disable-line declaration-no-important

    ~ .form-control-placeholder {
      cursor: not-allowed;
    }

    ~ .form-control-placeholder,
    &:hover ~ .form-control-placeholder,
    ~ .form-control-placeholder + .icon,
    &[disabled] ~ .form-control-placeholder + .btn-icon,
    ~ .form-control-help {
      color: $gray4 !important; // stylelint-disable-line declaration-no-important
    }
  }

  // deprecated - start
  &.form-crypto {
    ~ .form-control-placeholder ~ .form-control-help {
      padding-left: px-to-rem(50);
      text-decoration: underline;
    }

    ~ .form-control-help {
      &::before {
        position: absolute;
        bottom: px-to-rem(-8);
        left: 0;
        width: px-to-rem(50);
        color: $black;
        content: "";
      }

      &::after {
        position: absolute;
        bottom: 0;
        left: px-to-rem(15);
        display: flex;
        justify-content: center;
        width: px-to-rem(20);
        height: px-to-rem(20);
        color: $black;
        content: $custom-checkbox-indicator-icon-crypto;
      }
    }
  }
  // deprecated - end
}

// stylelint-disable selector-no-qualifying-type
// stylelint-disable property-no-vendor-prefix
select.form-control:not([size]):not([multiple]) {
  height: 4.375rem;
  padding: px-to-rem(34) px-to-rem(54) px-to-rem(13) 0;
  margin: 0;
  color: $black;
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  // stylelint-enable property-no-vendor-prefix

  .label-option-hidden {
    display: none;
  }

  &::-ms-expand {
    display: none;
  }
}

textarea.form-control {
  display: block;
  max-width: px-to-rem($large-form-group-max-width);
  height: px-to-rem(70);
  padding-top: px-to-rem(34);
  background-color: transparent;

  &.is-invalid {
    height: px-to-rem(71); // ajout du liseret d'erreur sans la scroll bar dans le textarea
  }

  &.limit-reached:focus {
    border-color: $black;
  }

  &:not(.form-control-empty),
  &:focus {
    height: px-to-rem(215);
    background-color: transparent;
  }

  ~ .form-control-placeholder::after {
    position: absolute;
    right: 15px;
    bottom: 8px;
    font-family: icon-orange, sans-serif;
    font-size: px-to-rem(24);
    font-weight: $font-weight-normal;
    color: $primary;
    content: $icon-Expand-screen;
  }

  &:disabled ~ .form-control-placeholder::after {
    color: $gray4;
  }

  ~ .form-control-placeholder,
  ~ .invalid-feedback,
  ~ .form-control-help,
  ~ .limit-reached-feedback {
    max-width: px-to-rem($large-form-group-max-width);
  }

  ~ .form-control-help .alert-icon,
  ~ .limit-reached-feedback .alert-icon {
    margin-right: ($spacer * .5);
    font-size: px-to-rem(20);
  }
}

select.form-control {
  max-width: px-to-rem($medium-form-group-max-width);
  cursor: pointer;
  background-color: transparent;

  ~ .form-control-placeholder {
    padding-bottom: 0;
    pointer-events: none;
    border-bottom: none;

    &::after {
      position: absolute;
      top: 2.4375rem;
      right: 0;
      padding-right: px-to-rem(15);
      padding-left: px-to-rem(15);
      font-family: icon-orange, sans-serif;
      font-size: px-to-rem(12);
      color: $primary;
      text-align: center;
      pointer-events: none;
      content: $icon-arrow-down;
    }

    ~ .form-control-help {
      color: $gray3;
    }
  }

  &:not(.form-control-empty),
  &:focus,
  &:active {
    height: px-to-rem(213);
    padding-top: 0;
    background-color: transparent;

    ~ .form-control-placeholder {
      height: 100%;
      padding-top: px-to-rem(12);
      padding-bottom: px-to-rem(3);
    }
  }

  &:disabled {
    ~ .form-control-placeholder::after {
      color: $gray4 !important; // stylelint-disable-line declaration-no-important
    }
  }

  ~ .form-control-placeholder,
  ~ .invalid-feedback,
  ~ .form-control-help {
    max-width: px-to-rem($medium-form-group-max-width);
    pointer-events: none;
  }
}
// stylelint-enable selector-no-qualifying-type

.limit-reached-feedback {
  display: none;
}

:-ms-input-placeholder.form-control { // stylelint-disable-line selector-no-vendor-prefix
  opacity: 1;
}
