.modal-dialog {
  max-width: 100%;
  height: 100%;

  .modal-content {
    display: block;
    min-height: 100%;
    padding: px-to-rem(69) px-to-rem(15);
  }

  .modal-header {
    position: absolute;
    top: px-to-rem(15);
    padding: px-to-rem(7) 0 px-to-rem(10);

    .close:not(.o-link-arrow) {
      display: none;

      &:hover,
      &:focus {
        color: $gray3;
      }

      &:active {
        color: $primary;
      }
    }

    .icon-delete {
      font-size: px-to-rem(15);
    }
  }

  .modal-body {
    h4,
    .h4 {
      margin-bottom: 0;
    }
  }

  .modal-footer {
    display: block;
    width: 100%;
    margin-top: px-to-rem(15);
    text-align: center;
  }
}

.ob1-modal-visual,
.modal-feedback {
  .modal-content {
    padding: 0 !important; // stylelint-disable-line declaration-no-important

    .modal-header {
      z-index: 1;
      padding-left: $spacer * .5;
    }
  }
}

.ob1-modal-content,
.ob1-feedback-content {
  padding: px-to-rem(30) px-to-rem(15);
}

.ob1-modal-body-visual,
.modal-feedback .feedback {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0;
}

.ob1-modal-img,
.ob1-modal-img img,
.ob1-feedback-functional-icon {
  width: 100%;
}

.ob1-modal-footer-visual {
  margin-top: px-to-rem(30);
  text-align: center;
}

.feedback-title {
  margin-top: 0;
}

@include media-breakpoint-down(sm) {
  .modal-dialog {
    margin: 0;
  }

  .modal-body {
    font-size: $lead-font-size-mobile;
    line-height: $lead-line-height-mobile;
  }
}

@include media-breakpoint-up(md) {
  .modal-dialog {
    max-width: calc((100% - 3.125%) * (5 / 6) - 30px); // 10/12ème du conteneur (sans les marges de sécurité, 2 * 1.5625%) - 30px de goutière
    height: auto;

    .modal-content {
      padding: px-to-rem(45);
    }

    .modal-header {
      top: px-to-rem(30);
      right: px-to-rem(37);
      padding: 0;

      .close {
        display: none;

        &:not(.o-link-arrow) {
          display: block;
          padding: 0;
        }
      }
    }
  }

  .ob1-modal-body-visual,
  .modal-feedback .feedback {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 0;
  }

  .ob1-modal-content,
  .ob1-feedback-content {
    width: 70%;
    padding: px-to-rem(45) px-to-rem(75) px-to-rem(45) px-to-rem(30);
  }

  .ob1-modal-img,
  .ob1-feedback-functional-icon {
    width: 30%;
  }

  .ob1-modal-img {
    display: flex;
    justify-content: center;
    overflow: hidden;

    img { // stylelint-disable-line selector-max-compound-selectors
      width: auto;
      min-height: 100%;
    }
  }

  .ob1-modal-footer-visual {
    text-align: left;
  }
}

@include media-breakpoint-up(lg) {
  .modal-dialog {
    max-width: calc((100% - 6.25%) * (5 / 6) - 30px); // 10/12ème du conteneur (sans les marges de sécurité, 2 * 3.125%) - 30px de goutière
  }
}

@include media-breakpoint-up(xl) {
  .modal-dialog {
    max-width: calc((100% - 6.25%) * (2 / 3) - 30px); // 8/12ème du conteneur (sans les marges de sécurité, 2 * 3.125%) - 30px de goutière

    .modal-content {
      padding: px-to-rem(45) px-to-rem(60);
    }
  }

  .ob1-modal-img,
  .ob1-feedback-functional-icon {
    width: 37.5%;
  }

  .ob1-modal-content,
  .ob1-feedback-content {
    width: 62.5%;
  }
}

@include media-breakpoint-up(xxl) {
  .modal-dialog {
    max-width: 870px; // 8/12ème de 1440px - 90px de marge
  }
}
