// Spinner à temps déterminé
.ob1-spinner-determined {
  position: relative;
  width: px-to-rem(18);
  height: px-to-rem(18);
  border: px-to-rem(3) solid;
  border-color: $primary $primary transparent transparent;
  border-radius: 50%;
  transform: rotate(45deg);
  animation: ob1-spinner-determined-scaleout 500ms linear 1s 1 normal forwards;

  .ob1-spinner-determined-loader1 {
    position: absolute;
    top: px-to-rem(-4);
    left: px-to-rem(6);
    z-index: 1;
    width: px-to-rem(10);
    height: px-to-rem(20);
    background: $white;
    transform: rotate(-45deg);
    transform-origin: left;
    animation: ob1-spinner-determined-loader1 500ms linear 0s 1 normal forwards;
  }

  .ob1-spinner-determined-loader2 {
    position: absolute;
    top: px-to-rem(-3);
    left: px-to-rem(-3);
    width: px-to-rem(18);
    height: px-to-rem(18);
    border: px-to-rem(3) solid;
    border-color: $primary $primary transparent transparent;
    border-radius: 50%;
    animation: ob1-spinner-determined-loader2 500ms linear 500ms 1 normal forwards;
  }

  &.ob1-spinner-inverse {
    border-color: $primary2 $primary2 transparent transparent;

    .ob1-spinner-determined-loader1 {
      background: $black;
      animation-name: ob1-spinner-determined-loader1-dark;
    }

    .ob1-spinner-determined-loader2 {
      border-color: $primary2 $primary2 transparent transparent;
    }
  }
}

@keyframes ob1-spinner-determined-loader1 {
  99.9% {
    background-color: $white;
  }
  100% {
    background-color: transparent;
    transform: rotate(135deg);
  }
}

@keyframes ob1-spinner-determined-loader1-dark {
  99.9% {
    background-color: $black;
  }
  100% {
    background-color: transparent;
    transform: rotate(135deg);
  }
}

@keyframes ob1-spinner-determined-loader2 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(180deg);
  }
}

@keyframes ob1-spinner-determined-scaleout {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

// Spinner à temps indéterminé
.ob1-spinner-undetermined {
  width: 50px;
  height: 50px;
  animation: ob1-spinner-undetermined-rotate 2s linear infinite;

  .ob1-spinner-undetermined-circle {
    fill: none;
    stroke: $primary;
    stroke-width: 5;
    animation: ob1-spinner-undetermined-dash 1.5s ease-in-out infinite;
  }

  &.ob1-spinner-inverse {
    .ob1-spinner-undetermined-circle {
      stroke: $primary2;
    }
  }
}

@keyframes ob1-spinner-undetermined-rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes ob1-spinner-undetermined-dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }

  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}

// Progress bar à temps déterminé
.ob1-progress-bar-determined {
  position: relative;
  overflow: hidden;
  background-color: $gray4;
  animation-name: ob1-progress-bar-open, none, ob1-progress-bar-determined-complete;
  animation-duration: 500ms, 1s, 500ms;
  animation-timing-function: linear, linear, linear;
  animation-delay: 0s, 500ms, 1.5s;
  animation-fill-mode: forwards, forwards, forwards;

  .ob1-progress-bar-determined-inner {
    position: absolute;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: $primary;
    animation-name: ob1-progress-bar-determined-inner-running;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-delay: 500ms;
    animation-fill-mode: forwards;
  }

  &.ob1-progress-bar-inverse {
    background-color: $gray3;

    .ob1-progress-bar-determined-inner {
      background-color: $primary2;
    }
  }
}

@keyframes ob1-progress-bar-open {
  0% {
    height: 0;
  }
  100% {
    height: px-to-rem(5);
  }
}

@keyframes ob1-progress-bar-determined-running {
  0% {
    background-position-x: 100%;
  }
  100% {
    background-position-x: 0;
  }
}

@keyframes ob1-progress-bar-determined-complete {
  0% {
    height: px-to-rem(5);
  }
  100% {
    height: px-to-rem(0);
  }
}

@keyframes ob1-progress-bar-determined-inner-running {
  0% {
    left: -100%;
  }
  100% {
    left: 0%;
  }
}

// Progress bar à temps indéterminé
.ob1-progress-bar-undetermined {
  animation-name: ob1-progress-bar-open, none;
  animation-duration: 500ms, infinite;
  animation-timing-function: linear, linear;
  animation-delay: 0s, 500ms;
  animation-fill-mode: forwards, forwards;

  @extend .ob1-progress-bar-determined;

  &::before,
  &::after {
    position: absolute;
    height: 100%;
    content: "";
    background-color: $primary;
  }

  &::before {
    animation: ob1-progress-bar-undetermined-increase 4s linear .5s infinite;
  }

  &::after {
    animation: ob1-progress-bar-undetermined-decrease 4s linear 2.5s infinite;
  }

  &.ob1-progress-bar-inverse {
    &::before,
    &::after {
      background-color: $primary2;
    }
  }
}

@keyframes ob1-progress-bar-undetermined-increase {
  0% {
    left: -5%;
    width: 5%;
  }

  50% {
    left: 130%;
    width: 100%;
  }

  100% {
    left: 130%;
    width: 100%;
  }
}

@keyframes ob1-progress-bar-undetermined-decrease {
  0% {
    left: -80%;
    width: 80%;
  }

  50% {
    left: 110%;
    width: 10%;
  }

  100% {
    left: 110%;
    width: 10%;
  }
}
