.ob1-menu-page {
  height: px-to-rem(52);
  border-bottom: 1px solid $gray4;

  button {
    background-color: transparent;
    border: 0;
  }

  .o-nav-light {
    flex-wrap: nowrap;
    border-bottom: none;

    &.ob1-nav-inline {
      overflow-x: auto;
      overflow-y: hidden;
    }

    .nav-item:first-child {
      margin-left: 0;
    }
  }

  .o-nav-light > .nav-item {
    flex-shrink: 0;

    .nav-link {
      position: relative;
      padding-bottom: px-to-rem(15);
    }

    .nav-link::before {
      position: absolute;
      bottom: 0;
      display: block;
      width: 0;
      content: "";
      border-bottom: px-to-rem(5) solid $primary;
    }

    &.active .nav-link::before { // stylelint-disable-line selector-max-class
      width: 100%;
      transition: width 400ms cubic-bezier(.33, 1, .68, 1);
    }

    &:not(.active):active .nav-link:not(.disabled):not(.o-link-arrow) {
      color: $primary;
    }
  }

  .o-nav-light > .nav-item > .nav-link {
    padding: px-to-rem(15) 0 px-to-rem(15);
    font-weight: $font-weight-bold;
    @extend .lead;
  }

  .o-nav-light > .nav-item:not(:first-child) > .nav-link {
    margin-left: px-to-rem(20);
  }
}

.ob1-menu-page-inverse {
  border-bottom-color: $dark;

  .o-nav-light > .nav-item > .nav-link {
    color: $white;

    &:hover {
      color: $gray4;
    }
  }
}

.ob1-menu-page-fullscreen {
  .o-nav-light > .nav-item {
    flex-grow: 1;
    text-align: center;

    .nav-link::before {
      left: 0;
    }
  }

  .o-nav-light > .nav-item > .nav-link {
    width: 100%;
    padding-right: $spacer * .5;
    padding-left: $spacer * .5;
    margin: 0 !important; // stylelint-disable-line declaration-no-important
  }
}

.ob1-menu-page-icon {
  height: px-to-rem(74);

  .o-nav-light > .nav-item {
    .nav-link {
      padding-top: px-to-rem(10);

      .item-icon { // stylelint-disable-line
        display: block;
        height: px-to-rem(22);
        margin-bottom: px-to-rem(5);
        font-size: px-to-rem(22);
      }
    }
  }
}

.ob1-sticky {
  background-color: $white;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .4);
}

.ob1-menu-page-center {
  border: none;

  .o-nav-light {
    justify-content: center;
  }

  .o-nav-light > .nav-item {
    border-bottom: 1px solid $gray4;
  }
}

.ob1-menu-page-dropdown {
  @extend .dropdown;

  &:hover,
  &:focus {
    .dropdown-menu {
      display: block;
    }

    .o-link-arrow::after {
      content: $icon-arrow-up;
    }
  }

  .o-link-arrow {
    &:hover,
    &:focus,
    &:active {
      color: $gray3;
      text-decoration: none;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $black;
    }

    &::after {
      color: currentcolor;
    }
  }

  .dropdown-menu {
    border: none;
  }
}

@include media-breakpoint-up(md) {
  .ob1-menu-page {
    .o-nav-light > .nav-item:not(:first-child) > .nav-link {
      margin-left: $spacer;
    }
  }
}

@include media-breakpoint-up(xl) {
  .ob1-menu-page {
    height: px-to-rem(59);

    .o-nav-light > .nav-item > .nav-link {
      padding-bottom: px-to-rem(20);
    }

    .o-nav-light > .nav-item:not(:first-child) > .nav-link {
      margin-left: $spacer * 1.5;
    }
  }

  .ob1-menu-page-icon {
    height: px-to-rem(84);

    .o-nav-light > .nav-item {
      .nav-link {
        .item-icon { // stylelint-disable-line
          height: px-to-rem(24);
          margin-bottom: px-to-rem(6);
          font-size: px-to-rem(24);
        }
      }
    }
  }
}
