.custom-checkbox {
  $custom-checkbox-icon-size: 20px !default;

  padding-left: px-to-rem(35);

  .custom-control-label {
    font-weight: $font-weight-normal;
    line-height: px-to-rem(22) !important; // stylelint-disable-line declaration-no-important
  }

  .custom-control-input,
  .custom-control-input:active {
    ~ .custom-control-label::before {
      width: $custom-checkbox-icon-size;
      height: $custom-checkbox-icon-size;
      font-size: $custom-checkbox-icon-size;
      line-height: $custom-checkbox-icon-size;
      color: $black;
      content: $icon-checkbox-non;
      background-color: transparent !important; // stylelint-disable-line declaration-no-important
    }

    &:checked {
      ~ .custom-control-label::before {
        color: $primary;
        content: $icon-checkbox-oui-bold;
      }
    }

    &:indeterminate {
      ~ .custom-control-label::before {
        color: $primary;
        content: $icon-checkbox-indetermine-bold;
      }
    }

    &:hover,
    &:focus {
      ~ .custom-control-label::before {
        color: $gray3;
      }
    }

    &:focus:not(:focus-visible):not(:hover) {
      ~ .custom-control-label::before {
        color: $black;
      }

      &:checked ~ .custom-control-label::before {
        color: $primary;
      }
    }

    &:active:not(:disabled) {
      ~ .custom-control-label::before {
        content: $icon-checkbox-non-bold;
      }

      &:checked ~ .custom-control-label::before {
        color: $gray3;
        content: $icon-checkbox-oui;
      }

      &:indeterminate ~ .custom-control-label::before {
        content: $icon-checkbox-indetermine;
      }
    }

    &:disabled {
      ~ .custom-control-label {
        color: $gray4;

        &::before {
          color: $gray4;
          cursor: not-allowed;
        }
      }
    }
  }

  .custom-control-input:not(:disabled):checked ~ .custom-control-label {
    &::before {
      border: none;
    }
  }

  .custom-control-input:checked ~ .custom-control-label {
    &::after {
      border: none;
    }
  }

  &.ob1-custom-checkbox-inverse {
    .custom-control-input,
    .custom-control-input:active {
      ~ .custom-control-label::before {
        color: $white;
      }

      &:checked,
      &:indeterminate {
        ~ .custom-control-label::before {
          color: $primary2;
        }
      }

      &:hover,
      &:focus {
        ~ .custom-control-label::before {
          color: $gray4;
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        ~ .custom-control-label::before {
          color: $white;
        }

        &:checked ~ .custom-control-label::before {
          color: $primary2;
        }
      }

      &:active:not(:disabled) {
        &:checked ~ .custom-control-label::before {
          color: $gray4;
        }
      }

      &:disabled {
        ~ .custom-control-label,
        ~ .custom-control-label::before {
          color: $gray3;
        }
      }
    }
  }
}
