.modal-dialog {
  .modal-header {
    .close {
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;
    }
  }
}

@include media-breakpoint-down(sm) {
  .modal-dialog {
    .modal-header {
      top: px-to-rem(20);
      padding: px-to-rem(3) 0 px-to-rem(6);
    }
  }
}
