.accordion {
  .accordion-card-title {
    min-height: px-to-rem(40);

    .accordion-title {
      padding-top: px-to-rem(8);
      padding-bottom: px-to-rem(11);
      line-height: px-to-rem(20);
    }

    .accordion-icon {
      &::before {
        font-size: px-to-rem(12);
      }
    }
  }

  .ob1-accordion-large {
    .item-icon {
      font-size: px-to-rem(28);
    }

    .accordion-title {
      padding-top: px-to-rem(11);
      padding-bottom: px-to-rem(15);
      font-size: $h3-font-size;
      line-height: $h3-line-height;
    }

    .accordion-icon::before {
      font-size: px-to-rem(14);
    }
  }

}

.accordion-in-block {

  .accordion-title {
    padding-top: px-to-rem(9);
  }

  .card:last-child .card-header,
  .card .card-header {
    a,
    button {
      border: 0;
    }
  }

  .card.card-expanded:last-child {
    border-bottom: 0;
  }
}

@include media-breakpoint-between(md, xl) {
  .accordion {
    .accordion-card-body.lead {
      margin-bottom: px-to-rem(20);
    }
  }
}

@include media-breakpoint-up(xl) {
  .accordion-card-body.lead {
    margin-bottom: px-to-rem(20);
  }
}
