.dropdown {
  button:focus {
    outline: px-to-rem(2) solid $primary;
    outline-offset: 1px;
  }
}

.dropdown-menu {
  box-shadow: $dropdown-box-shadow;

  &[x-placement="top-start"] {
    border-bottom: px-to-rem(5) solid $primary;
  }

  &[x-placement="bottom-start"] {
    border-top: px-to-rem(5) solid $primary;
  }
}

.dropdown-item {
  font-weight: $font-weight-normal;
  line-height: px-to-rem(22);
  text-decoration: none; /* surcharge o-link pour retro compat 1.13 */
  outline-offset: px-to-rem(4);
  transition: outline-offset .15s ease-in-out;

  &.o-link { // deprecated
    padding-left: $dropdown-item-padding-x;
  }

  &.active {
    font-weight: $font-weight-bold;
  }

  &:focus {
    outline: px-to-rem(2) solid $primary;
    outline-offset: 0;
  }

  &:hover:focus,
  &:hover,
  &:focus,
  &:active,
  &.active:active,
  &.active:hover:focus:active {
    font-weight: $font-weight-bold;
    color: $dropdown-link-hover-color;
    text-decoration: underline;
    background: transparent;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $black;
    text-decoration: none;
  }

  &:active,
  &:hover:focus:active {
    color: $primary;
  }
}

.dropdown-header {
  padding: px-to-rem(9) px-to-rem(35) px-to-rem(6) px-to-rem(20);
  font-size: $font-size-transverse;
  font-weight: $font-weight-bold;
  line-height: $line-height-transverse;
  color: $gray3;

  ~ .dropdown-item {
    padding: px-to-rem(9) px-to-rem(35);
  }
}

.dropdown-item + .dropdown-header {
  padding-top: px-to-rem(21);
}

.ob1-dropdown-item-checkbox {
  .custom-control {
    margin-bottom: 0;

    .custom-control-label {
      font-weight: $font-weight-normal;
    }
  }

  &:hover .custom-control-input:not(:disabled) ~ .custom-control-label,
  &:focus .custom-control-input:not(:disabled) ~ .custom-control-label {
    color: $black;
  }
}

.ob1-dropdown-small {
  .dropdown-item {
    padding: px-to-rem(10) px-to-rem(20);
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;
  }
}
