.form-data {
  .ob1-filled-label {
    font-size: $font-size-transverse3;
    line-height: $line-height-transverse3;
  }

  .ob1-filled-data {
    padding: px-to-rem(5) 0;
  }
}

.form-control {
  height: px-to-rem(50);
  padding: px-to-rem(23) px-to-rem(10) px-to-rem(5) 0;
  font-size: $font-size-transverse2;
  line-height: $line-height-transverse2;

  ~ .form-control-placeholder {
    padding: px-to-rem(23) px-to-rem(50) px-to-rem(5) 0;
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;

    + .icon {
      margin-left: px-to-rem(-30);
      font-size: px-to-rem(20);
    }
  }

  ~ .form-control-help:not(.alert-container) {
    padding-top: px-to-rem(5);
    font-size: $font-size-transverse3;
    line-height: $line-height-transverse3;
  }

  &.form-control-icon {
    // padding-right: px-to-rem(54);

    ~ .form-control-placeholder + .btn-icon {
      margin-left: px-to-rem(-35);
      font-size: px-to-rem(20);
    }
  }

  &:not(.form-control-empty),
  &:focus {
    height: px-to-rem(31);
    padding-top: px-to-rem(5);
    padding-bottom: px-to-rem(5);
    margin-top: px-to-rem(18);

    ~ .form-control-placeholder {
      height: px-to-rem(18);
      padding-top: 0;
      font-size: $font-size-transverse3;
      line-height: $line-height-transverse3;
    }
  }
}

// stylelint-disable selector-no-qualifying-type
textarea.form-control {
  height: px-to-rem(50);
  padding-top: px-to-rem(23);

  &.is-invalid {
    height: px-to-rem(51); // ajout du liseret d'erreur sans la scroll bar dans le textarea
  }

  &:not(.form-control-empty),
  &:focus {
    height: px-to-rem(188);
  }

  ~ .form-control-placeholder::after {
    right: 10px;
    bottom: 0;
    font-size: px-to-rem(20);
  }
}

select.form-control:not([size]):not([multiple]) {
  height: px-to-rem(50);
  padding: px-to-rem(23) px-to-rem(50) px-to-rem(6) 0;

}

select.form-control {
  ~ .form-control-placeholder {
    &::after {
      top: px-to-rem(23);
      padding-right: px-to-rem(10);
      padding-left: px-to-rem(20);
    }

  }

  &:not(.form-control-empty),
  &:focus,
  &:active {
    margin-bottom: px-to-rem(5);

    ~ .form-control-placeholder {
      padding-top: px-to-rem(5);
    }
  }

}

// stylelint-enable selector-no-qualifying-type
