// Accessibilité : Permet d'avoir un bloc de texte avec un lien placé sur le titre du bloc tout en ayant la totalité du bloc cliquable
.composite-link {
  position: relative;
  a::after,
  button::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    content: "";
  }
}

.ob1-cursor-initial {
  cursor: initial;
}

// Supprime l'outline ajouté au focus si l'élément reçoit le focus via la souris et non au clavier
.js-focus-visible :not([data-focus-visible-added]).focus,
.js-focus-visible :not([data-focus-visible-added]):focus {
  /* stylelint-disable-next-line scss/selector-no-redundant-nesting-selector */
  &,
  + .toggle,
  ~ .ob1-filterchip-label,
  ~ .custom-control-label::before,
  + .btn,
  ~ .ob1-floating-action-btn-label {
    outline: 0 !important; // stylelint-disable-line declaration-no-important
  }
}

:focus:not(:focus-visible) {
  &,
  + .toggle,
  ~ .ob1-filterchip-label,
  ~ .custom-control-label::before {
    outline: 0 !important; // stylelint-disable-line declaration-no-important
  }
}

// ==========================================================================
// Visuel
// ==========================================================================
.ob1-visual {
  box-shadow: $push-spacer $push-spacer $gray2;
}

.ob1-visual-full {
  width: calc(100% - #{$push-spacer});
}

.ob1-visual-white {
  box-shadow: $push-spacer $push-spacer $white;
}

.ob1-visual-white-left {
  box-shadow: -$push-spacer $push-spacer $white;
}

.ob1-visual-gray1 {
  box-shadow: $push-spacer $push-spacer $gray1;
}

.ob1-visual-gray1-left {
  box-shadow: -$push-spacer $push-spacer $gray1;
}

.ob1-visual-black {
  box-shadow: $push-spacer $push-spacer $black;
}

.ob1-visual-black-left {
  box-shadow: -$push-spacer $push-spacer $black;
}

@include media-breakpoint-up(md) {
  .ob1-visual {
    box-shadow: $push-spacer-md $push-spacer-md $gray2;
  }

  .ob1-visual-left {
    margin-left: $push-spacer-md;
    box-shadow: -$push-spacer-md $push-spacer-md $gray2;
  }

  .ob1-visual-full {
    width: calc(100% - #{$push-spacer-md});
  }

  .ob1-visual-white {
    box-shadow: $push-spacer-md $push-spacer-md $white;
  }

  .ob1-visual-white-left {
    margin-left: $push-spacer-md;
    box-shadow: -$push-spacer-md $push-spacer-md $white;
  }

  .ob1-visual-gray1 {
    box-shadow: $push-spacer-md $push-spacer-md $gray1;
  }

  .ob1-visual-gray1-left {
    margin-left: $push-spacer-md;
    box-shadow: -$push-spacer-md $push-spacer-md $gray1;
  }

  .ob1-visual-black {
    box-shadow: $push-spacer-md $push-spacer-md $black;
  }

  .ob1-visual-black-left {
    margin-left: $push-spacer-md;
    box-shadow: -$push-spacer-md $push-spacer-md $black;
  }
}

.ob1-sticky {
  position: fixed;
  top: 0;
  z-index: 999;
}
