.ob1-filterchip {
  .ob1-filterchip-label {
    display: inline-block;
    padding: $filterchip-padding-top $filterchip-padding-x $filterchip-padding-bottom;
    margin-bottom: 0;
    font-size: $font-size-transverse2;
    font-weight: $font-weight-normal;
    line-height: $line-height-transverse2;
    color: $black;
    text-align: center;
    pointer-events: auto;
    cursor: pointer;
    background-color: transparent;
    border: px-to-rem(1) solid $gray4;
    border-radius: px-to-rem(16);
    outline-offset: px-to-rem(4);
    transition: outline-offset .15s ease-in-out;

    &::before {
      padding-right: 8px;
      font-family: $icomoon-font-family;
      font-size: $small-font-size;
      font-weight: $font-weight-light;
      line-height: 1rem;
      content: none;
    }

    // Pseudo élément caché ajouté afin de définir la largeur du label égale à la largeur du texte en gras dans tous les états
    &::after {
      display: block;
      height: 0;
      overflow: hidden;
      font-weight: $font-weight-bold;
      visibility: hidden;
      content: attr(data-title);
    }
  }

  .ob1-filterchip-input {
    &:hover,
    &:focus,
    &:active {
      ~ .ob1-filterchip-label {
        font-weight: $font-weight-bold;
        color: $gray3;
        border-color: $gray3;
      }
    }

    &:focus:not(:focus-visible):not(:hover) ~ .ob1-filterchip-label {
      font-weight: $font-weight-normal;
      color: $black;
      border-color: $gray4;
    }

    &:focus ~ .ob1-filterchip-label {
      outline: px-to-rem(2) solid $primary;
      outline-offset: 0;
    }

    &:active ~ .ob1-filterchip-label {
      padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
      border-width: px-to-rem(2);
    }

    &:checked {
      ~ .ob1-filterchip-label {
        padding: $filterchip-selected-padding-top $filterchip-selected-padding-x $filterchip-selected-padding-bottom;
        font-weight: $font-weight-bold;
        text-decoration: underline;
        text-decoration-thickness: px-to-rem(2);
        text-decoration-color: transparent;
        text-underline-offset: px-to-rem(3);
        border-color: $primary;
        border-width: px-to-rem(2);


        &::before {
          display: inline-block;
          font-weight: $font-weight-bold;
          color: $primary;
          text-decoration: none;
          content: $icon-checkbox-tick;
        }
      }

      &:hover,
      &:focus {
        ~ .ob1-filterchip-label {
          color: $gray3;
          border-color: $gray3;

          &::before {
            color: $gray3;
          }
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        ~ .ob1-filterchip-label {
          font-weight: $font-weight-bold;
          color: $black;
          border-color: $primary;

          &::before {
            color: $primary;
          }
        }
      }

      &:active ~ .ob1-filterchip-label {
        padding: $filterchip-padding-top px-to-rem(14) $filterchip-padding-bottom;
        color: $gray3;
        border-color: $gray3;
        border-width: px-to-rem(1);
      }
    }

    &:disabled {
      ~ .ob1-filterchip-label {
        color: $gray4;
        cursor: not-allowed;
        border-color: $gray4;
        border-width: px-to-rem(1);
      }

      &:hover,
      &:focus,
      &:active {
        ~ .ob1-filterchip-label {
          padding: $filterchip-padding-top $filterchip-padding-x $filterchip-padding-bottom;
          font-weight: $font-weight-normal;
        }
      }

      &:checked {
        ~ .ob1-filterchip-label {
          border-width: px-to-rem(2);

          &::before {
            color: $gray4;
          }
        }

        &:hover,
        &:focus,
        &:active {
          ~ .ob1-filterchip-label {
            padding: $filterchip-selected-padding-top $filterchip-selected-padding-x $filterchip-selected-padding-bottom;
            font-weight: $font-weight-bold;
            color: $gray4;
            border-color: $gray4;
          }
        }
      }
    }
  }

  &.ob1-filterchip-selected .ob1-filterchip-label {
    padding: $filterchip-selected-padding-top $filterchip-selected-padding-x $filterchip-selected-padding-bottom;
    font-weight: $font-weight-bold;
    border-color: $primary;
    border-width: px-to-rem(2);

    &::before {
      font-weight: $font-weight-bold;
      color: $primary;
      content: $icon-checkbox-tick;
    }

    &:hover,
    &:focus {
      color: $gray3;
      border-color: $gray3;

      &::before {
        color: $gray3;
      }
    }

    &:active {
      padding: $filterchip-padding-top px-to-rem(14) $filterchip-padding-bottom;
      color: $gray3;
      border-color: $gray3;
      border-width: px-to-rem(1);
    }
  }

  &.ob1-filterchip-reverse {
    .ob1-filterchip-label {
      color: $white;
      border-color: $gray3;
    }

    .ob1-filterchip-input {
      &:hover,
      &:focus,
      &:active {
        ~ .ob1-filterchip-label { // stylelint-disable-line selector-max-class
          color: $gray4;
          border-color: $gray4;
          outline-color: $primary-official;
        }
      }

      &:focus:not(:focus-visible):not(:hover) ~ .ob1-filterchip-label {
        color: $white;
        border-color: $gray3;
      }

      &:checked {
        ~ .ob1-filterchip-label {
          border-color: $primary-official;

          &::before {
            color: $primary-official;
          }
        }

        &:hover,
        &:focus,
        &:active {
          ~ .ob1-filterchip-label {
            color: $gray4;
            border-color: $gray4;

            &::before {
              color: $gray4;
            }
          }
        }

        &:focus:not(:focus-visible):not(:hover) {
          ~ .ob1-filterchip-label {
            // font-weight: $font-weight-bold;
            color: $white;
            border-color: $primary2;

            &::before {
              color: $primary2;
            }
          }
        }
      }

      &:disabled,
      &:disabled:checked {
        ~ .ob1-filterchip-label {
          color: $gray3;
          border-color: $gray3;

          &::before {
            color: $gray3;
          }
        }
      }
    }

    &.ob1-filterchip-selected .ob1-filterchip-label {
      border-color: $primary-official;

      &::before {
        color: $primary-official;
      }

      &:hover,
      &:focus,
      &:active {
        color: $gray4;
        border-color: $gray4;

        &::before {
          color: $gray4;
        }
      }
    }
  }

  &.ob1-choicechip {
    .ob1-filterchip-label {
      &::before {
        content: none !important; // stylelint-disable-line declaration-no-important
      }
    }

    .ob1-filterchip-input {
      &:active ~ .ob1-filterchip-label {
        padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
      }

      &:checked {
        ~ .ob1-filterchip-label {
          padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
        }

        &:active {
          ~ .ob1-filterchip-label {
            padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
            border-width: px-to-rem(2);
          }
        }
      }

      &:disabled {
        ~ .ob1-filterchip-label {
          padding: $filterchip-padding-top $filterchip-padding-x $filterchip-padding-bottom;
        }

        &:checked {
          ~ .ob1-filterchip-label {
            padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
          }
        }
      }
    }

    &.ob1-filterchip-selected .ob1-filterchip-label {
      padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;

      &:active {
        padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
        border-width: px-to-rem(2);
      }
    }
  }

  &.ob1-inputchip {
    .icon-delete {
      margin-left: 11px;
      font-size: px-to-rem(10);
    }

    .ob1-filterchip-label {
      padding: px-to-rem(5) px-to-rem(13) px-to-rem(7) px-to-rem(25);
      font-weight: $font-weight-bold;
      background: $gray2;
      border: none;

      &:hover,
      &:focus,
      &:focus-visible {
        .ob1-inputchip-label {
          text-decoration-line: line-through;
        }

        .icon-delete {
          color: $gray3;
        }
      }

      &:focus-visible {
        outline: px-to-rem(2) solid $primary;
        outline-offset: 0;
      }

      &:focus:not(:focus-visible):not(:hover) {
        .ob1-inputchip-label {
          text-decoration-line: none;
        }

        .icon-delete {
          color: $black;
        }
      }

      &:active {
        .ob1-inputchip-label {
          text-decoration-line: line-through;
        }

        .icon-delete {
          color: $primary;
        }
      }

      &:disabled {
        pointer-events: all;
        cursor: not-allowed;
        background: $gray4;

        .ob1-inputchip-label,
        .icon-delete {
          color: $white;
          text-decoration-line: none;
        }
      }
    }
  }

  &.ob1-inputchip-inverse {
    .icon-delete {
      margin-left: 11px;
      font-size: px-to-rem(10);
    }

    .ob1-filterchip-label {
      padding: px-to-rem(5) px-to-rem(13) px-to-rem(7) px-to-rem(25);
      font-weight: $font-weight-bold;
      color: $white;
      background: $gray1;
      border: none;

      &:hover,
      &:focus,
      &:focus-visible {
        .ob1-inputchip-label {
          text-decoration-line: line-through;
        }

        .icon-delete {
          color: $gray4;
        }
      }

      &:focus-visible {
        outline: px-to-rem(2) solid $primary-official;
        outline-offset: 0;
      }

      &:focus:not(:focus-visible):not(:hover)
       {
        .ob1-inputchip-label {
          text-decoration-line: none;
        }

        .icon-delete {
          color: $white;
        }
      }

      &:active {
        .ob1-inputchip-label{
          text-decoration-line: line-through;
        }

        .icon-delete {
          color: $primary-official;
        }
      }

      &:disabled {
        pointer-events: all;
        cursor: not-allowed;
        background: $gray3;

        .ob1-inputchip-label,
        .icon-delete{
          color: $black !important; // stylelint-disable-line declaration-no-important
          text-decoration-line: none;
        }
      }
    }
  }
}

.ob1-color-chips {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: px-to-rem(-15); // Nécessaire pour ne pas avoir de marge de 15px sur la dernière ligne de pastilles

  .ob1-color-chip-input {
    @extend .custom-control-input;

    &:focus + .ob1-color-chip-label {
      border-color: $gray3;
      border-width: px-to-rem(1);
      outline: px-to-rem(1) dotted $primary; // TODO : il faudra mettre à jour cette propriété lorsqu'OB1 héritera de la version de Boosted ayant un outline à "2px solid black"
    }

    &:checked + .ob1-color-chip-label {
      border-color: $primary;
      border-width: px-to-rem(2);
    }
  }

  .ob1-color-chip-label {
    display: flex;
    align-items: center;
    justify-content: center;
    width: px-to-rem(32);
    height: px-to-rem(32);
    margin-right: px-to-rem(15);
    margin-bottom: px-to-rem(15);
    border: px-to-rem(1) solid $gray4;
    border-radius: 50%;

    &:last-of-type {
      margin-right: 0;
    }

    &:hover {
      cursor: pointer;
      border-color: $gray3;
      border-width: px-to-rem(1);
    }

    &:active {
      border-color: $gray3;
      border-width: px-to-rem(2);
    }
  }

  .ob1-color-chip {
    width: px-to-rem(22);
    height: px-to-rem(22);
    border-radius: 50%;
  }

  &.ob1-color-chips-mini {
    .ob1-color-chip {
      position: relative;
      width: px-to-rem(16);
      height: px-to-rem(16);
      margin-right: px-to-rem(18);
      margin-bottom: px-to-rem(15);
      border: px-to-rem(1) $gray4 solid;
      &:last-of-type {
        margin-right: 0;
      }
      .ob1-color-chips-mini-tooltip {
        position: absolute;
        top: px-to-rem(20);
        left: -10000vw;
        padding: px-to-rem(2) px-to-rem(5);
        background-color: $gray4;
      }
      &:hover {
        .ob1-color-chips-mini-tooltip {
          left: px-to-rem(20);
          z-index: 1;
        }
      }
    }
  }
}
