.capsule, // deprecated
.ob1-capsule {
  display: inline-block;
  padding: px-to-rem(3) px-to-rem(15) px-to-rem(5);
  margin-right: px-to-rem(15);
  font-size: $font-size-transverse2;
  font-weight: $font-weight-bold;
  line-height: $line-height-transverse2;
  color: $black;
  background-color: $success;
  border-radius: px-to-rem(14);

  &.inactive, // deprecated
  &.disabled, // deprecated
  &.ob1-capsule-inactive, // deprecated
  &.ob1-capsule-disabled {
    background-color: $gray4;
  }

  &.ob1-capsule-context {
    background-color: $gray2;
  }

  &.ob1-capsule-inverse {
    &.disabled, // deprecated
    &.ob1-capsule-disabled {
      color: $white;
      background-color: $gray3;
    }

    &.ob1-capsule-context {
      color: $white;
      background-color: $gray1;
    }
  }
}

.bg-gray1,
.bg-gray2 {
  .ob1-capsule {
    &.ob1-capsule-context {
      background-color: $white;
    }

    &.ob1-capsule-inverse {
      &.ob1-capsule-context {
        background-color: $black;
      }
    }
  }
}
