/**
 * Cards list
 */

.ob1-item-card-list {
  @extend .items-list;
}

.ob1-item-card {
  @extend .items-list-item;

  max-width: 100%;
  min-height: px-to-rem(70);
  padding: px-to-rem(14) px-to-rem(15);
  border: 1px solid $gray4;

  .ob1-item-card-container {
    @extend .item-container;
    min-height: auto;
    border-bottom: none;

    .ob1-item-card-text {
      @extend .item-text;
      span:last-child {
        align-items: normal;
        min-height: auto;
      }
    }
  }

  .ob1-item-card-icon {
    @extend .item-icon;
  }

  .ob1-item-card-img {
    @extend .item-img;
  }

  .ob1-item-card-status {
    @extend .item-status;
  }

  .ob1-item-card-desc {
    @extend .item-desc;
  }

  .ob1-item-card-price {
    display: flex;
    margin-top: px-to-rem(3);
    font-size: $font-size-transverse2;
    line-height: px-to-rem(20);
  }

  &:hover {
    border-color: $primary;

    .ob1-item-card-text {
      color: $dark;
      // stylelint-disable declaration-no-important
      text-decoration: none !important;
      // stylelint-enable declaration-no-important
    }
  }

  &:active .ob1-item-card-container {
    .ob1-item-card-text {
      text-decoration: underline;
      text-decoration-color: $primary;
    }

    .ob1-item-card-desc {
      color: $primary;
    }
  }

}
