.search-field-results {
  max-width: px-to-rem($medium-form-group-max-width);
  padding: px-to-rem(6) 0;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .4);

  .result-line {
    display: block;
    padding: px-to-rem(7) 0 px-to-rem(11) px-to-rem(15);
    font-size: $font-size-transverse;
    line-height: $line-height-transverse;
    cursor: pointer;

    &.result-line-focused {
      border: 2px solid $primary;
    }

    &:hover,
    &:focus {
      color: $gray3;
      text-decoration: underline;
      text-decoration-color: $gray3;
    }

    &:active {
      color: $primary;
      text-decoration: underline;
      text-decoration-color: $primary;
    }
  }
}

.search-field-loading {
  max-width: px-to-rem($medium-form-group-max-width);
  padding: px-to-rem(41) 0 px-to-rem(36);
  text-align: center;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .4);

  .ob1-spinner-undetermined {
    width: px-to-rem(34);
    height: px-to-rem(34);
  }

  p {
    margin: 0;
  }
}

.flag-icon {
  font-size: px-to-rem(21);
}

span.search-country-selected { // stylelint-disable-line selector-no-qualifying-type
  position: absolute;
  top: px-to-rem(37);
  left: 0;
}

input.search-country-selected { // stylelint-disable-line selector-no-qualifying-type
  padding-left: px-to-rem(41);
}
