.ob1-price {
  $price-font-size-primary: px-to-rem(20);
  $price-line-height-primary: px-to-rem(24);
  $price-padding-top-primary: px-to-rem(5);
  $price-padding-bottom-primary: px-to-rem(0);

  &.ob1-price-primary {
    .ob1-price-amount {
      padding-top: $price-padding-top-primary;
      padding-bottom: $price-padding-bottom-primary;
      font-size: $price-font-size-primary;
      line-height: $price-line-height-primary;
      color: $primary;
    }

    .ob1-price-by-month {
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;
    }

  }

  &.ob1-price-secondary {
    .ob1-price-amount {
      font-size: $font-size-transverse;
      line-height: $line-height-transverse;
      color: $black;
    }

    .ob1-price-by-month {
      font-size: $font-size-transverse;
      line-height: $line-height-transverse;
    }

    &.ob1-price-inverse {
      .ob1-price-amount {
        color: $white;
      }
    }
  }

  .ob1-price-mention {
    display: block;
    font-size: $small-font-size;
    line-height: $small-line-height;
    color: $gray3;
  }

  &.ob1-price-inverse {
    .ob1-price-mention {
      color: $gray4;
    }

    .ob1-price-by-month {
      color: $white;
    }

    .ob1-price-amount {
      color: $primary-official;
    }
  }
}
