.ob1-card {
  position: relative;
  border: px-to-rem(1) solid $gray4;
  box-shadow: px-to-rem(2) px-to-rem(2) $gray4;

  &:not(.ob1-card-multiple) {
    .accordion .card-header a {
      position: unset;
    }

    a::before,
    button::before,
    label::before {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      content: "";
    }
  }

  &:hover,
  &.focus-within {
    border-color: $gray3;
    box-shadow: px-to-rem(2) px-to-rem(2) $gray3;
  }

  &:focus-within {
    border-color: $gray3;
    box-shadow: px-to-rem(2) px-to-rem(2) $gray3;
  }

  &.focus-within:not(:focus-visible):not(:hover) {
    border-color: $gray4;
    box-shadow: px-to-rem(2) px-to-rem(2) $gray4;
  }

  &:focus-within:not(:focus-visible):not(:hover) {
    border-color: $gray4;
    box-shadow: px-to-rem(2) px-to-rem(2) $gray4;
  }

  &:active {
    border-color: $primary;
    box-shadow: px-to-rem(2) px-to-rem(2) $primary;
  }

  &.ob1-card-disabled {
    color: $gray4;
    pointer-events: none;
  }

  &.ob1-card-selected {
    border-color: $primary;
    box-shadow: px-to-rem(2) px-to-rem(2) $primary;

    &:hover,
    &.focus-within,
    &:active {
      border-color: $gray3;
      box-shadow: px-to-rem(2) px-to-rem(2) $gray3;
    }

    &:focus-within {
      border-color: $gray3;
      box-shadow: px-to-rem(2) px-to-rem(2) $gray3;
    }

    &.focus-within:not(:focus-visible):not(:hover) {
      border-color: $primary;
      box-shadow: px-to-rem(2) px-to-rem(2) $primary;
    }

    &:focus-within:not(:focus-visible):not(:hover) {
      border-color: $primary;
      box-shadow: px-to-rem(2) px-to-rem(2) $primary;
    }
  }

  &.ob1-card-inverse {
    color: $white;
    background-color: $dark;
    border-color: $gray3;
    box-shadow: px-to-rem(2) px-to-rem(2) $gray3;

    &:hover,
    &.focus-within {
      border-color: $gray4;
      box-shadow: px-to-rem(2) px-to-rem(2) $gray4;
    }

    &:focus-within {
      border-color: $gray4;
      box-shadow: px-to-rem(2) px-to-rem(2) $gray4;
    }

    &:active {
      border-color: $primary2;
      box-shadow: px-to-rem(2) px-to-rem(2) $primary2;
    }

    &.ob1-card-disabled {
      color: $gray3;
    }

    &.ob1-card-selected {
      border-color: $primary2;
      box-shadow: px-to-rem(2) px-to-rem(2) $primary2;

      &:hover,
      &.focus-within,
      &:active {
        border-color: $gray4;
        box-shadow: px-to-rem(2) px-to-rem(2) $gray4;
      }

      &:focus-within {
        border-color: $gray4;
        box-shadow: px-to-rem(2) px-to-rem(2) $gray4;
      }
    }
  }

  &.ob1-card-link {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &::after {
      padding-top: px-to-rem(3);
      margin-left: px-to-rem(10);
      font-family: icon-orange, sans-serif;
      font-size: px-to-rem(12);
      color: $primary;
      content: $icon-arrow-next;
    }
  }

  // stylelint-disable declaration-no-important -- shorter to use important in this case
  &.ob1-card-multiple {
    border-color: $gray4 !important;
    box-shadow: none !important;

    &.ob1-card-inverse {
      border-color: $gray3 !important;
    }
  }
  // stylelint-enable declaration-no-important
}

a.ob1-card { // stylelint-disable-line selector-no-qualifying-type
  &:hover,
  &:focus {
    color: $black;
  }
}
