$skeleton-animation-color: #ebebeb !default;
$skeleton-animation-color-gray1: #4b4b4b !default;
$skeleton-animation-color-gray2: #e0e0e0 !default;
$skeleton-animation-color-dark: #1f1f1f !default;
$background-color: #f2f2f2 !default;
$background-color-gray1: #515151 !default;
$background-color-gray2: #e7e7e7 !default;
$background-color-dark: #262626 !default;

.ob1-skeleton {
  .ob1-skeleton {

    &-white {
      @include ob1-skeleton-animation($background-color, $skeleton-animation-color);
    }

    &-gray1 {
      @include ob1-skeleton-animation($background-color-gray1, $skeleton-animation-color-gray1);
    }

    &-gray2 {
      @include ob1-skeleton-animation($background-color-gray2, $skeleton-animation-color-gray2);
    }

    &-dark {
      @include ob1-skeleton-animation($background-color-dark, $skeleton-animation-color-dark);
    }
  }

  .ob1-skeleton-margin {
    margin-top: px-to-rem(5);
    margin-bottom: px-to-rem(5);
  }
}

.ob1-skeleton-line // deprecated
{
  @extend %ob1-skeleton-line-size;
  @include ob1-skeleton-animation($background-color, $skeleton-animation-color);
}

.ob1-skeleton-square // deprecated
{
  @extend %ob1-skeleton-square-size;
  @include ob1-skeleton-animation($background-color, $skeleton-animation-color);
}

%ob1-skeleton-square-size {
  &.ob1-skeleton-small {
    width: px-to-rem(30);
    height: px-to-rem(30);
  }

  &.ob1-skeleton-medium {
    width: px-to-rem(45);
    height: px-to-rem(45);
  }

  &.ob1-skeleton-large {
    width: px-to-rem(225);
    height: px-to-rem(225);
  }
}

%ob1-skeleton-line-size {
  width: 100%;

  &.ob1-skeleton-small {
    height: px-to-rem(15);
  }

  &.ob1-skeleton-medium {
    height: px-to-rem(30);
  }

  &.ob1-skeleton-large {
    height: px-to-rem(45);
  }
}
